"use strict";
var quest1=[];quest1[0]={key:'b',q:'Đối tượng nghiên cứu môn học giáo dục quốc phòng, an ninh:',ans1:'A. Nghiên cứu về chiến lược xây dựng và bảo vệ Tổ quốc của Đảng, Nhà nước trong tình hình mới.',ans2:'B. Nghiên cứu về đường lối quân sự của Đảng, Công tác quốc phòng an ninh, Quân sự và kỹ năng quân sự cần thiết.',ans3:'C. Nghiên cứu về chiến lược kinh tế, quốc phòng của Đảng, Nhà nước trong sự nghiệp đổi mới.',ans4:'D. Nghiên cứu về chiến lược kinh tế, quốc phòng, an ninh và đối ngoại của Đảng, Nhà nước.'};quest1[1]={key:'a',q:'Quá trình nghiên cứu môn học giáo dục quốc phòng, an ninh phải nắm vững và vận dụng đúng đắn các quan điểm tiếp cận khoa học:',ans1:'A. Hệ thống, lịch sử, logic, thực tiễn.',ans2:'B. Khách quan, lịch sử, toàn diện.',ans3:'C. Hệ thống, biện chứng, lịch sử, logic.',ans4:'D. Lịch sử, cụ thể biện chứng.'};quest1[2]={key:'d',q:'Các phương pháp nghiên cứu môn học giáo dục quốc phòng, an ninh:',ans1:'A. Nghiên cứu lý thuyết, nghiên cứu thực tiễn.',ans2:'B. Nghiên cứu tập trung, kết hợp với thảo luận nhóm.',ans3:'C. Kết hợp các phương pháp dạy học lý thuyết và thực hành.',ans4:'D. Cả A và C.'};quest1[3]={key:'a',q:'Theo quan điểm của chủ nghĩa Mác - Lênin về chiến tranh:',ans1:'A. Là một hiện tượng chính trị xã hội có tính lịch sử.',ans2:'B. Là những cuộc xung đột tự phát ngẫu nhiên.',ans3:'C. Là một hiện tượng xã hội mang tính vĩnh viễn.',ans4:'D. Là những xung đột do những mâu thuẫn không mang tính xã hội.'};quest1[4]={key:'b',q:'Theo quan điểm của chủ nghĩa Mác - Lênin về nguồn gốc của chiến tranh:',ans1:'A. Chiến tranh bắt nguồn từ khi xuất hiện ngay từ khi xuất hiện xã hội loài người.',ans2:'B. Chiến tranh bắt nguồn từ khi xuất hiện chế độ tư hữu, có giai cấp và nhà nước.',ans3:'C. Chiến tranh bắt nguồn từ sự phát triển tất yếu khách quan của loài người.',ans4:'D. Chiến tranh bắt nguồn từ khi xuất hiện các hình thức tôn giáo.'};quest1[5]={key:'c',q:'Theo quan điểm của chủ nghĩa Mác - Lênin về bản chất của chiến tranh:',ans1:'A. Là tiếp tục mục tiêu kinh tế bằng thủ đoạn bạo lực.',ans2:'B. Là thủ đoạn để đạt được mục tiêu của một giai cấp.',ans3:'C. Là tiếp tục của chính trị bằng thủ đoạn bạo lực.',ans4:'D. Là thủ đoạn chính trị của một giai cấp.'};quest1[6]={key:'a',q:'Theo quan điểm của chủ nghĩa Mác – Lênin, chính trị là sự phản ánh tập trung của:',ans1:'A. Kinh tế.',ans2:'B. Xã hội.',ans3:'C. Quốc phòng.',ans4:'D. An ninh.'};quest1[7]={key:'c',q:'Theo quan điểm của chủ nghĩa Mác - Lênin về quan hệ giữa chiến tranh với chính trị:',ans1:'A. Chính trị là con đường, là phương tiện của chiến tranh.',ans2:'B. Chính trị là một thời đoạn, một bộ phận của chiến tranh.',ans3:'C. Chính trị chi phối và quyết định toàn bộ tiến trình và kết cục của chiến tranh.',ans4:'D. Chính trị không thế sử dụng kết quả sau chiến tranh để đề ra nhiệm vụ, mục tiêu.'};quest1[8]={key:'c',q:'Trong mối quan hệ giữa chiến tranh và chính trị, thì chiến tranh là kết quả phản ánh:',ans1:'A. Những bản chất chính trị xã hội.',ans2:'B. Sức mạnh tổng hợp của quân đội.',ans3:'C. Những cố gắng cao nhất của chính trị.',ans4:'D. Những cố gắng cao nhất về kinh tế.'};quest1[9]={key:'d',q:'Hồ Chí Minh khẳng định mục đích cuộc chiến tranh của nhân dân ta chống thực dân Pháp:',ans1:'A. Bảo vệ nhân dân, bảo vệ chế độ, bảo vệ Tổ quốc Việt Nam xã hội chủ nghĩa.',ans2:'B. Bảo vệ đất nước và chống ách đô hộ của thực dân, đế quốc.',ans3:'C. Bảo vệ tính mạng, tài sản của nhân dân, của chế độ xã hội chủ nghĩa.',ans4:'D. Bảo vệ độc lập chủ quyền và thống nhất đất nước.'};quest1[10]={key:'d',q:'Tư tưởng Hồ Chí Minh xác định thái độ đối với chiến tranh là:',ans1:'A. Phản đối tất cả các cuộc chiến tranh.',ans2:'B. Phản đối các cuộc chiến tranh chống áp bức, nô dịch.',ans3:'C. Phản đối các cuộc chiến tranh sắc tộc tôn giáo.',ans4:'D. Ủng hộ chiến tranh chính nghĩa, phản đối chiến tranh phi nghĩa.'};quest1[11]={key:'c',q:'Theo tư tưởng Hồ Chí Minh nhất thiết phải sử dụng bạo lực cách mạng:',ans1:'A. Để lật đổ xã hội cũ, xây dựng xã hội mới.',ans2:'B. Để xây dựng chế độ mới.',ans3:'C. Để giành lấy chính quyền và bảo vệ chính quyền.',ans4:'D. Để lật đổ chế độ cũ.'};quest1[12]={key:'a',q:'Theo tư tưởng Hồ Chí Minh, bạo lực cách mạng được tạo bởi:',ans1:'A. Sức mạnh của toàn dân, bằng cả lực lượng chính trị và lực lượng vũ trang.',ans2:'B. Sức mạnh của toàn dân, bằng cả tiềm lực chính trị và tiềm lực kinh tế.',ans3:'C. Kết hợp chặt chẽ giữa đấu tranh chính trị và đấu tranh ngoại giao.',ans4:'D. Tất cả đều đúng.'};quest1[13]={key:'d',q:'Lời kêu gọi toàn quốc kháng chiến chống thực dân Pháp của Hồ Chí Minh:',ans1:'A. Ngày 22/12/1944.',ans2:'B. Ngày 23/11/1945.',ans3:'C. Ngày 02/9/1945.',ans4:'D. Ngày 19/12/1946.'};quest1[14]={key:'a',q:'Theo quan điểm của chủ nghĩa Mác - Lênin về bản chất giai cấp của quân đội phụ thuộc vào:',ans1:'A. Bản chất của các nhà nước đã tổ chức ra quân đội đó.',ans2:'B. Bản chất của các giai cấp và của nhà nước đã tổ chức ra quân đội đó.',ans3:'C. Bản chất của giai cấp công nông và của nhà nước đã tổ chức ra quân đội đó.',ans4:'D. Tất cả đều đúng.'};quest1[15]={key:'a',q:'Nguyên tắc quan trọng nhất về xây dựng quân đội kiểu mới của Lê nin là:',ans1:'A. Sự lãnh đạo của Đảng Cộng sản đối với quân đội.',ans2:'B. Giữ vững quan điểm giai cấp trong xây dựng quân đội.',ans3:'C. Tính kỷ luật cao là yếu tố quyết định sức mạnh quân đội.',ans4:'D. Quân đội chính quy, hiện đại, trung thành với giai cấp công nhân.'};quest1[16]={key:'c',q:'Lê nin khẳng định yếu tố giữ vai trò quyết định đến sức mạnh chiến đấu của quân đội là:',ans1:'A. Quân số, tổ chức, cơ cấu biên chế.',ans2:'B. Chất lượng vũ khí, trang bị kỹ thuật.',ans3:'C. Chính trị tinh thần.',ans4:'D. Trình độ huấn luyện và thể lực.'};quest1[17]={key:'a',q:'Chủ tịch Hồ Chí Minh khẳng định sự ra đời của Quân đội nhân dân Việt Nam là:',ans1:'A. Là một tất yếu có tính quy luật trong đấu tranh giai cấp, đấu tranh dân tộc ở Việt Nam.',ans2:'B. Là một hiện tượng ngẫu nhiên trong quá trình đấu tranh cách mạng của dân tộc Việt Nam.',ans3:'C. Là một sự kế thừa trong lịch sử chống giặc ngoại xâm.',ans4:'D. Là một hiện tượng tự phát do đòi hỏi của chiến tranh cách mạng.'};quest1[18]={key:'c',q:'Theo tư tưởng Hồ Chí Minh, quân đội nhân dân Việt Nam:',ans1:'A. Mang bản chất của giai cấp nông dân.',ans2:'B. Mang bản chất giai cấp công – nông.',ans3:'C. Mang bản chất của giai cấp công nhân.',ans4:'D. Mang bản chất nhân dân lao động Việt Nam.'};quest1[19]={key:'c',q:'Quân đội nhân dân Việt Nam mang bản chất của giai cấp công nhân, đồng thời có:',ans1:'A. Tính quần chúng, cách mạng sâu sắc.',ans2:'B. Tính phong phú và đa dạng.',ans3:'C. Tính nhân dân và tính dân tộc sâu sắc.',ans4:'D. Tính phổ biến và rộng rãi.'};quest1[20]={key:'a',q:'Chủ tịch Hồ Chí Minh khẳng định Quân đội nhân dân Việt Nam có nhiệm vụ:',ans1:'A. Xây dựng quân đội ngày càng hùng mạnh và sẵn sàng chiến đấu.',ans2:'B. Xây dựng quân đội ngày càng hùng hậu và sẵn sàng chiến đấu.',ans3:'C. Xây dựng quân đội ngày càng đông đảo và sẵn sàng chiến đấu.',ans4:'D. Xây dựng quân đội có chất lượng cao và sẵn sàng chiến đấu.'};quest1[21]={key:'c',q:'Chủ tịch Hồ Chí Minh khẳng định một trong hai nhiệm vụ chính của quân đội ta là:',ans1:'A. Tiến hành phổ biến chính sách của Đảng, Nhà nước cho nhân dân.',ans2:'B. Giúp nhân dân cải thiện đời sống vật chất tinh thần.',ans3:'C. Thiết thực tham gia lao động sản xuất, góp phần xây dựng chủ nghĩa xã hội.',ans4:'D. Làm nòng cốt phát triển kinh tế tại nơi đóng quân.'};quest1[22]={key:'c',q:'Theo tư tưởng Hồ Chí Minh, Quân đội nhân dân Việt Nam có những chức năng:',ans1:'A. Chiến đấu và sẵn sàng chiến đấu.',ans2:'B. Chiến đấu, lao động sản xuất.',ans3:'C. Chiến đấu, công tác, lao động sản xuất.',ans4:'D. Chiến đấu tham gia giữ gìn hòa bình khu vực.'};quest1[23]={key:'b',q:'Quan điểm đầu tiên của chủ nghĩa Mác - Lênin về bảo vệ Tổ quốc xã hội chủ nghĩa:',ans1:'A. Bảo vệ tổ quốc xã hội chủ nghĩa là nhiệm vụ thường xuyên liên tục.',ans2:'B. Bảo vệ tổ quốc xã hội chủ nghĩa là tất yếu khách quan.',ans3:'C. Bảo vệ tổ quốc xã hội chủ nghĩa là nhiệm vụ cấp thiết trước mắt.',ans4:'D. Bảo vệ tổ quốc xã hội chủ nghĩa là nhiệm vụ trọng yếu của toàn dân.'};quest1[24]={key:'a',q:'Bác Hồ nói với Đại đoàn quân tiên phong trong lần về thăm Đền Hùng năm 1954:',ans1:'A. Các vua Hùng đã có công dựng nước, Bác cháu ta phải cùng nhau giữ lấy nước.',ans2:'B. Các vua Hùng đã có công dựng nước, Bác cháu ta phải cùng nhau bảo vệ đất nước.',ans3:'C. Các vua Hùng đã có công dựng nước, Bác cháu ta phải cùng nhau xây dựng đất nước.',ans4:'D. Các vua Hùng đã có công dựng nước, Bác cháu ta phải cùng nhau bảo vệ Tổ quốc.'};quest1[25]={key:'d',q:'Theo tư tưởng Hồ Chí Minh, mục tiêu bảo vệ Tổ quốc Việt Nam xã hội chủ nghĩa là:',ans1:'A. Sự nghiệp đổi mới.',ans2:'B. Sự nghiệp công nghiệp hóa hiện đại hóa.',ans3:'C. Bản sắc văn hóa dân tộc.',ans4:'D. Độc lập dân tộc và chủ nghĩa xã hội.'};quest1[26]={key:'c',q:'Tư tưởng Hồ Chí Minh về sức mạnh bảo vệ Tổ quốc:',ans1:'A. Là sức mạnh của cả dân tộc, kết hợp với sức mạnh của nền quốc phòng toàn dân.',ans2:'B. Là sức mạnh tổng hợp của cả dân tộc, cả nước kết hợp với sức mạnh thời đại.',ans3:'C. Là sức mạnh của toàn dân, lấy lực lượng vũ trang làm nòng cốt.',ans4:'D. Là sức mạnh của lực lượng vũ trang nhân dân, sức mạnh quốc phòng toàn dân.'};quest1[27]={key:'c',q:'Vai trò lãnh đạo sự nghiệp bảo vệ Tổ quốc Việt Nam xã hội chủ nghĩa thuộc về:',ans1:'A. Các đoàn thể, các tổ chức chính trị xã hội.',ans2:'B. Lực lượng vũ trang nhân dân Việt Nam.',ans3:'C. Đảng Cộng sản Việt Nam.',ans4:'D. Toàn bộ hệ thống chính trị ở Việt Nam.'};quest1[28]={key:'b',q:'Đảng ta khẳng định vị trí của nền quốc phòng toàn dân, an ninh nhân dân:',ans1:'A. Xây dựng kinh tế là chủ yếu, quốc phòng, an ninh là thứ yếu.',ans2:'B. Chỉ coi trọng quốc phòng, an ninh khi đât nước có chiến tranh.',ans3:'C. Luôn luôn coi trọng quốc phòng, an ninh, coi đó là nhiệm vụ chiến lược gắn bó chặt chẽ.',ans4:'D. Luôn luôn coi trọng quốc phòng, an ninh, coi đó là nền tảng để xây dựng đất nước.'};quest1[29]={key:'a',q:'Đặc trưng đầu tiên của nền quốc phòng toàn dân, an ninh nhân dân:',ans1:'A. Mang tính chất tự vệ do giai cấp công nhân tiến hành.',ans2:'B. Chỉ có mục đích duy nhất là tự vệ chính đáng.',ans3:'C. Vững mạnh toàn diện để tự vệ chính đáng.',ans4:'D. Được xây dựng hiện đại có sức mạnh tổng hợp.'};quest1[30]={key:'d',q:'Đặc trưng mang tính truyền thống của nền quốc phòng toàn dân, an ninh nhân dân:',ans1:'A. Nền quốc phòng, an ninh vì dân, của dân và toàn thể nhân dân tiến hành.',ans2:'B. Nền quốc phòng, an ninh mang tính giai cấp, dân tộc sâu sắc.',ans3:'C. Nền quốc phòng, an ninh bảo vệ quyền lợi của dân.',ans4:'D. Nền quốc phòng, an ninh do nhân dân xây dựng, mang tính chất nhân dân sâu sắc.'};quest1[31]={key:'a',q:'Sức mạnh của nền quốc phòng toàn dân, an ninh nhân dân bao gồm:',ans1:'A. Sức mạnh do các yếu tố chính trị, văn hóa, khoa học.',ans2:'B. Sức mạnh quốc phòng, an ninh hiện đại.',ans3:'C. Sức mạnh của quân đội nhân dân, công an nhân dân.',ans4:'D. Có sức mạnh tổng hợp do nhiều yếu tố tạo thành.'};quest1[32]={key:'b',q:'Mục đích xây dựng nền quốc phòng toàn dân, an ninh nhân dân là:',ans1:'A. Tạo sức mạnh tổng hợp và tạo thế chủ động cho sự nghiệp xây dựng và bảo vệ Tổ quốc.',ans2:'B. Tạo ra sức mạnh quân sự để ngăn chặn nguy cơ chiến tranh.',ans3:'C. Tạo ra tiềm lực kinh tế để phòng thủ đất nước.',ans4:'D. Tạo ra môi trường hòa bình để phát triển đất nước theo định hướng XHCN.'};quest1[33]={key:'c',q:'Nhiệm vụ xây dựng nền quốc phòng toàn dân và an ninh nhân dân là:',ans1:'A. Xây dựng các cấp chính quyền ở cơ sở và lực lượng dân quân tự vệ vững mạnh.',ans2:'B. Xây dựng lực lượng quốc phòng, an ninh đáp ứng yêu cầu bảo vệ vững chắc Tổ quốc Việt Nam XHCN.',ans3:'C. Xây dựng lực lượng công an, quân đội vững mạnh.',ans4:'D. Xây dựng tiềm lực quân sự, an ninh vững mạnh.'};quest1[34]={key:'a',q:'Hai nhiệm vụ chiến lược của cách mạng Việt Nam hiện nay là:',ans1:'A. Xây dựng và phát triển kinh tế xã hội ngày càng vững mạnh.',ans2:'B. Xây dựng đất nước và phát triển kinh tế.',ans3:'C. Xây dựng và bảo vệ Tổ quốc Việt Nam xã hội chủ nghĩa.',ans4:'D. Xây dựng và phát triển kinh tế, quốc phòng an ninh nhân dân.'};quest1[35]={key:'b',q:'Lực lượng của nền Quốc phòng toàn dân, an ninh nhân dân bao gồm:',ans1:'A. Lực lượng toàn dân, lực lượng vũ trang nhân dân.',ans2:'B. Lực lượng quân đội nhân dân và công an nhân dân.',ans3:'C. Lực lượng toàn dân và dân quân tự vệ.',ans4:'D. Lực lượng dự bị động viên và dân quân tự vệ.'};quest1[36]={key:'a',q:'Tiềm lực quốc phòng – an ninh là:',ans1:'A. Khả năng về của cải vật chất có thể huy động để thực hiện nhiệm vụ quốc phòng, an ninh.',ans2:'B. Khả năng về nhân lực, vật lực, tài chính có thể huy động để thực hiện nhiệm vụ QP-AN.',ans3:'C. Khả năng về tài chính có thể huy động phục vụ cho nhiệm vụ quốc phòng, an ninh.',ans4:'D. Khả năng về phương tiện kỹ thuật có thể huy động thực hiện nhiệm vụ quốc phòng an ninh.'};quest1[37]={key:'c',q:'Tiềm lực quốc phòng, an ninh được thể hiện ở tất cả các lĩnh vực đời sống xã hội nhưng tập trung ở:',ans1:'A. Tiềm lực chính trị, tinh thần; khoa học và công nghệ; kinh tế; quân sự, an ninh.',ans2:'B. Tiềm lực chính trị, tinh thần; đối ngoại, khoa học và công nghệ.',ans3:'C. Tiềm lực công nghiệp quốc phòng, khoa học quân sự.',ans4:'D. Tiềm lực chính trị, tinh thần; văn hóa xã hội; kinh tế.'};quest1[38]={key:'d',q:'Tiềm lực chính trị, tinh thần của nền quốc phòng toàn dân – an ninh nhân dân:',ans1:'A. Là khả năng về chính trị, tinh thần của xã hội để thực hiện nhiệm vụ quốc phòng.',ans2:'B. Là khả năng về chính trị, ý chí, quyết tâm chiến đấu chống quân xâm lược của toàn dân.',ans3:'C. Là khả năng về chính trị, tinh thần có thể huy động nhằm tạo thành sức mạnh để thực hiện nhiệm vụ quốc phòng an ninh.',ans4:'D. Là khả năng về chính trị, tinh thần của nhân dân được huy động để thực hiện nhiệm vụ quốc phòng an ninh.'};quest1[39]={key:'c',q:'Nội dung xây dựng tiềm lực chính trị, tinh thần của nền quốc phòng toàn dân, an ninh nhân dân:',ans1:'A. Xây dựng lòng yêu nước, niềm tin vào Đảng, Nhà nước, chế độ xã hội chủ nghĩa.',ans2:'B. Xây dựng hệ thống chính trị trong sạch, vững mạnh, phát huy quyền làm chủ của nhân dân.',ans3:'C. Xây dựng khối đại đoàn kết toàn dân, nâng cao cảnh giác cách mạng, thực hiện tốt giáo dục QP-AN.',ans4:'D. Tất cả đều đúng.'};quest1[40]={key:'d',q:'Tiềm lực kinh tế của nền quốc phòng toàn dân, an ninh nhân dân:',ans1:'A. Khả năng về tài chính và khoa học công nghệ để phục vụ nhiệm vụ quốc phòng, an ninh.',ans2:'B. Khả năng về trang bị kỹ thuật quân sự có thể huy động để phục vụ nhiệm vụ quốc phòng, an ninh.',ans3:'C. Khả năng về kinh tế của đất nước có thể khai thác, huy động nhằm phục vụ cho quốc phòng, an ninh.',ans4:'D. Khả năng về tài chính để phục vụ nhiệm vụ quốc phòng, an ninh.'};quest1[41]={key:'b',q:'Nội dung xây dựng tiềm lực kinh tế của nền nền quốc phòng toàn dân, an ninh nhân dân:',ans1:'A. Đẩy mạnh công nghiệp hóa, hiện đại hóa đất nước, xây dựng nền kinh tế độc lập tự chủ.',ans2:'B. Kết hợp chặt chẽ phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh, xây dựng hạ tầng kinh tế với cơ sở hạ tầng quốc phòng.',ans3:'C. Có kế hoạch chuyển sản xuất từ thời bình sang thời chiến và duy trì sự phát triển của nền kinh tế.',ans4:'D. Tất cả đều đúng.'};quest1[42]={key:'d',q:'Xây dựng tiềm lực khoa học công nghệ của nền quốc phòng toàn dân, an ninh nhân dân:',ans1:'A. Tạo nên khả năng về vũ khí trang bị kỹ thuật để phòng thủ đất nước.',ans2:'B. Tạo nên khả năng về khoa học công nghệ của quốc gia có thể khai thác, huy động để phục vụ quốc phòng, an ninh.',ans3:'C. Tạo nên khả năng để huy động đội ngũ cán bộ khoa học kỹ thuật phục vụ quốc phòng, an ninh.',ans4:'D. Tạo nên khả năng ứng dụng kết quả nghiên cứu khoa học công nghệ vào quốc phòng, an ninh.'};quest1[43]={key:'a',q:'Một trong những nội dung xây dựng tiềm lực quân sự, an ninh:',ans1:'A. Xây dựng lực lượng quân đội vững mạnh toàn diện.',ans2:'B. Xây dựng lực lượng công an vững mạnh toàn diện.',ans3:'C. Xây dựng lực lượng thường trực, dân quân tự vệ vững mạnh.',ans4:'D. Xây dựng lực lượng vũ trang nhân dân vững mạnh toàn diện.'};quest1[44]={key:'a',q:'Thế trận quốc phòng toàn dân, an ninh nhân dân là:',ans1:'A. Sự tổ chức, bố trí lực lượng, tiềm lực mọi mặt của đất nước trên toàn bộ lãnh thổ.',ans2:'B. Sự bố trí con người và vũ khí trang bị phù hợp trên toàn bộ lãnh thổ.',ans3:'C. Sự bố trí thế trận sẵn sàng tác chiến trên một địa bàn chiến lược.',ans4:'D. Sự bố trí các đơn vị của lực lượng vũ trang trên toàn bộ lãnh thổ.'};quest1[45]={key:'b',q:'Một trong những nội dung xây dựng thế trận quốc phòng toàn dân, an ninh nhân dân là:',ans1:'A. Tổ chức phòng thủ dân sự, kết hợp cải tạo địa hình với xây dựng hạ tầng và các công trình quốc phòng, an ninh.',ans2:'B. Tổ chức phòng thủ dân sự, kết hợp xây dựng các khu vực hậu phương, vùng căn cứ vững chắc về mọi mặt.',ans3:'C. Tổ chức phòng thủ dân sự, chủ động tiến công tiêu diệt địch trên tất cả các mặt trận.',ans4:'D. Tổ chức phòng thủ dân sự bảo đảm an toàn cho người và của cải vật chất.'};quest1[46]={key:'c',q:'Biện pháp chính nhằm xây dựng nhận thức về nền quốc phòng toàn dân, an ninh nhân dân là:',ans1:'A. Thường xuyên giáo dục ý thức, trách nhiệm của công dân.',ans2:'B. Thường xuyên thực hiện giáo dục nghĩa vụ công dân.',ans3:'C. Thường xuyên thực hiện giáo dục quốc phòng, an ninh.',ans4:'D. Thường xuyên phổ biến nhiệm vụ quốc phòng an ninh.'};quest1[47]={key:'d',q:'Nội dung thực hiện giáo dục quốc phòng, an ninh:',ans1:'A. Giáo dục về âm mưu thủ đoạn của địch.',ans2:'B. Giáo dục về tình yêu quê hương, đất nước, chế độ xã hội chủ nghĩa.',ans3:'C. Giáo dục đường lối quan điểm của Đảng, pháp luật của nhà nước về quốc phòng, an ninh.',ans4:'D. Cả A, B, C.'};quest1[48]={key:'d',q:'Mục đích của chiến tranh nhân dân Việt Nam bảo vệ Tổ quốc là:',ans1:'A. Bảo vệ vững chắc độc lập, chủ quyền, thống nhất và toàn vẹn lãnh thổ, bảo vệ an ninh quốc gia, trật tự an toàn xã hội và nền văn hóa.',ans2:'B. Bảo vệ Đảng, Nhà nước, nhân dân và chế độ xã hội chủ nghĩa, bảo vệ lợi ích quốc gia dân tộc.',ans3:'C. Bảo vệ sự nghiệp đổi mới, công nghiệp hóa, hiện đại hóa đất nước, giữ vững ổn định chính trị và môi trường hòa bình, phát triển đất nước theo định hướng xã hội chủ nghĩa.',ans4:'D. Tất cả đều đúng.'};quest1[49]={key:'b',q:'Đối tượng tác chiến của chiến tranh nhân dân Việt Nam bảo vệ Tổ quốc là:',ans1:'A. Chủ nghĩa đế quốc và các lực lượng ly khai dân tộc trên thế giới.',ans2:'B. Chủ nghĩa đế quốc và các thế lực phản động có hành động phá hoại, xâm lược, lật đổ cách mạng.',ans3:'C. Chủ nghĩa đế quốc và các thế lực phản cách mạng trên thế giới.',ans4:'D. Lực lượng khủng bố quốc tế và lực lượng phản động trong nước.'};quest1[50]={key:'d',q:'Âm mưu, thủ đoạn chủ yếu của kẻ thù khi xâm lược nước ta:',ans1:'A. Đánh nhanh, thắng nhanh.',ans2:'B. Lực lượng tham gia với quân số đông, vũ khí trang bị hiện đại.',ans3:'C. Sử dụng biện pháp chính trị, ngoại giao để lừa bịp dư luận.',ans4:'D. Tất cả đều đúng.'};quest1[51]={key:'d',q:'Khi tiến hành chiến tranh xâm lược nước ta, địch có điểm yếu:',ans1:'A. Tiến hành một cuộc chiến tranh phi nghĩa sẽ bị thế giới lên án.',ans2:'B. Phải tác chiến trong điều kiện địa hình, thời tiết phức tạp.',ans3:'C. Phải đương đầu với dân tộc ta có truyền thống yêu nước, chống xâm lược.',ans4:'D. Tất cả đều đúng.'};quest1[52]={key:'b',q:'Tính chất cơ bản chiến tranh nhân dân Việt Nam bảo vệ Tổ quốc là:',ans1:'A. Cuộc chiến tranh toàn dân, lấy lực lượng vũ trang làm nòng cốt.',ans2:'B. Cuộc chiến tranh toàn dân, toàn diện, dưới sự lãnh đạo của Đảng Cộng sản Việt Nam.',ans3:'C. Cuộc chiến tranh toàn diện lấy mặt trận quân sự làm yếu tố quyết định.',ans4:'D. Cuộc chiến tranh cách mạng chống các thế lực phản cách mạng.'};quest1[53]={key:'c',q:'Tính chất chiến tranh nhân dân bảo vệ Tổ quốc là:',ans1:'A. Cuộc chiến tranh của giai cấp công nhân nhằm bảo vệ độc lập chủ quyền và lãnh thổ.',ans2:'B. Cuộc chiến tranh cách mạng nhằm bảo vệ biên giới quốc gia.',ans3:'C. Cuộc chiến tranh chính nghĩa, tự vệ cách mạng nhằm bảo vệ Tổ quốc Việt Nam XHCN.',ans4:'D. Cuộc chiến tranh tự vệ nhằm đánh thắng các thế lực xâm lược để bảo vệ chủ quyền, lãnh thổ đất nước.'};quest1[54]={key:'c',q:'Tính chất hiện đại trong chiến tranh nhân dân bảo vệ Tổ quốc được thể hiện:',ans1:'A. Sử dụng vũ khí trang bị hiện đại để tiến hành chiến tranh.',ans2:'B. Sử dụng vũ khí trang bị hiện đại để đánh bại kẻ thù có vũ khí hiện đại hơn.',ans3:'C. Hiện đại về vũ khí, trang bị, tri thức và nghệ thuật quân sự.',ans4:'D. Kết hợp sử dụng vũ khí tương đối hiện đại với vũ khí hiện đại.'};quest1[55]={key:'a',q:'Đặc điểm về cường độ của chiến tranh nhân dân bảo vệ Tổ quốc:',ans1:'A. Diễn ra khẩn trương, quyết liệt phức tạp ngay từ đầu và suốt quá trình chiến tranh.',ans2:'B. Diễn ra khẩn trường, quy mô lớn giai đoạn đầu của chiến tranh.',ans3:'C. Diễn ra trong bối cảnh quốc tế có những thuận lợi cho chúng ta.',ans4:'D. Diễn ra với nhịp độ cao, cường độ lớn giai đoạn giữa của cuộc chiến tranh.'};quest1[56]={key:'c',q:'Vị trí, ý nghĩa của quan điểm “toàn dân đánh giặc” trong chiến tranh nhân dân bảo vệ Tổ Quốc:',ans1:'A. Điều kiện để mỗi người dân được tham gia đánh giặc, giữ nước.',ans2:'B. Điều kiện để phát huy cao nhất yếu tố con người trong cuộc chiến tranh.',ans3:'C. Điều kiện để phát huy cao nhất sức mạnh tổng hợp trong cuộc chiến tranh.',ans4:'D. Điều kiện để phát huy sức mạnh toàn dân.'};quest1[57]={key:'b',q:'Trong tiến hành chiến tranh toàn diện, mặt trận đấu tranh nào là chủ yếu:',ans1:'A. Mặt trận kinh tế.',ans2:'B. Mặt trận quân sự.',ans3:'C. Mặt trận ngoại giao.',ans4:'D. Mặt trận chính trị.'};quest1[58]={key:'d',q:'Quan điểm của Đảng ta về chuẩn bị cho chiến tranh nhân dân bảo vệ Tổ Quốc:',ans1:'A. Chuẩn bị đầy đủ mọi mặt trên các vùng chiến lược của đất nước.',ans2:'B. Chuẩn bị con người, vũ khí trang bị cho chiến tranh.',ans3:'C. Chuẩn bị đầy đủ tiềm lực kinh tế, quân sự trên cả nước cũng như từng khu vực.',ans4:'D. Chuẩn bị mọi mặt trên cả nước, cũng như từng khu vực để bảo vệ vững chắc Tổ quốc Việt Nam XHCN.'};quest1[59]={key:'d',q:'Nội dung chủ yếu của chiến tranh nhân dân:',ans1:'A. Tổ chức thế trận chiến tranh nhân dân.',ans2:'B. Tổ chức lực lượng chiến tranh nhân dân.',ans3:'C. Phối hợp chặt chẽ chống quân địch tiến công từ bên ngoài vào và bạo loạn lật đổ bên trong.',ans4:'D. Tất cả đều đúng.'};quest1[60]={key:'a',q:'Thế trận chiến tranh nhân dân:',ans1:'A. Sự tổ chức, bố trí lực lượng để tiến hành chiến tranh và hoạt động tác chiến.',ans2:'B. Sự tổ chức, bố trí các đơn vị của lực lượng vũ trang nhân dân đánh giặc.',ans3:'C. Sự tổ chức, bố trí lực lượng phòng thủ đất nước.',ans4:'D. Sự tổ chức, bố trí các lực lượng chiến đấu trên chiến trường.'};quest1[61]={key:'b',q:'Trong chiến tranh nhân dân bảo vệ Tổ quốc, thế trận của chiến tranh được:',ans1:'A. Bố trí rộng trên cả nước, nhưng phải tập trung cho khu vực chủ yếu.',ans2:'B. Bố trí rộng trên cả nước, nhưng phải có trọng tâm, trọng điểm.',ans3:'C. Bố trí rộng trên cả nước, nhưng tập trung ở các vùng kinh tế trọng điểm.',ans4:'D. Bố trí rộng trên cả nước, nhưng tập trung ở các địa bàn trọng điểm.'};quest1[62]={key:'b',q:'Lực lượng chiến tranh nhân dân là:',ans1:'A. Các quân khu, quân đoàn chủ lực.',ans2:'B. Toàn dân, lấy lực lượng vũ trang ba thứ quân làm nòng cốt.',ans3:'C. Lực lượng lục quân, hải quân, phòng không không quân.',ans4:'D. Lực lượng quân đội nhân dân và công an nhân dân.'};quest1[63]={key:'c',q:'Lực lượng toàn dân trong tiến hành chiến tranh nhân dân được tổ chức chặt chẽ thành:',ans1:'A. Lực lượng quân đội nhân dân và công an nhân dân.',ans2:'B. Lực lượng quân đội nhân dân và dân quân tự vệ.',ans3:'C. Lực lượng quần chúng rộng rãi và lực lượng quân sự.',ans4:'D. Lực lượng đấu tranh chính trị và đấu tranh quân sự.'};quest1[64]={key:'b',q:'Tiến hành chiến tranh nhân dân bảo vệ Tổ quốc phải phối hợp chặt chẽ giữa:',ans1:'A. Chống quân xâm lược từ bên ngoài vào với chống lực lượng khủng bố từ bên trong.',ans2:'B. Chống quân địch tấn công từ bên ngoài vào với bạo loạn lật đổ từ bên trong.',ans3:'C. Chống bạo loạn lật đổ với trấn áp bọn phản động.',ans4:'D. Chống bạo loạn lật đổ với các hoạt động phá hoại khác.'};quest1[65]={key:'a',q:'Lực lượng vũ trang nhân dân gồm các tổ chức:',ans1:'A. Vũ trang và bán vũ trang.',ans2:'B. Quốc phòng và an ninh.',ans3:'C. Quân sự và an ninh trật tự.',ans4:'D. An ninh trật tự và bán vũ trang.'};quest1[66]={key:'a',q:'Lực lượng vũ trang nhân dân Việt Nam bao gồm:',ans1:'A. Quân đội nhân dân, công an nhân dân, dân quân tự vệ.',ans2:'B. Quân đội nhân dân, dự bị động viên, dân quân tự vệ.',ans3:'C. Quân đội thường trực, dự bị động viên, dân quân tự vệ.',ans4:'D. Quân đội chủ lực, cảnh sát nhân dân, dân quân tự vệ.'};quest1[67]={key:'d',q:'Khó khăn lớn tác động đến xây dựng lực lượng vũ trang nhân dân:',ans1:'A. Các nước trên thế giới đang chạy đua vũ trang rất quyết liệt.',ans2:'B. Mặt trái kinh tế thị trường tác động quá lớn đến lực lượng vũ trang nhân dân.',ans3:'C. Chủ nghĩa đế quốc và các lực lượng khủng bố đẩy mạnh chiến lược “bạo loạn lật đổ”.',ans4:'D. Chủ nghĩa đế quốc và các thế lực phản động đẩy mạnh chiến lược “diễn biến hòa bình”.'};quest1[68]={key:'a',q:'Một trong những thuận lợi cơ bản trong xây dựng lực lượng vũ trang nhân dân:',ans1:'A. Nhân dân ta có truyền thống yêu nước, đoàn kết, tin tưởng vào sự lãnh đạo của Đảng.',ans2:'B. Nhân dân ta luôn thương yêu đùm bọc, tin tưởng tuyệt đối vào sự lãnh đạo quân đội.',ans3:'C. Nhân dân ta có truyền thống đánh giặc độc đáo, sáng tạo.',ans4:'D. Nhân dân ta có tinh thần đoàn kết quốc tế, được quốc tế ủng hộ.'};quest1[69]={key:'a',q:'Một trong những thách thức lớn tác động đến xây dựng lực lượng vũ trang nhân dân:',ans1:'A. Tụt hậu xa hơn về kinh tế so với nhiều nước trong khu vực và trên thế giới.',ans2:'B. Tụt hậu xa hơn về khoa học công nghệ so với nhiều nước trong khu vực và trên thế giới.',ans3:'C. Các thế lực thù địch tập trung chống phá ta về chính trị, tư tưởng.',ans4:'D. Các thế lực thù địch tập trung chống phá lực lượng vũ trang nhân dân.'};quest1[70]={key:'a',q:'Quan điểm, nguyên tắc cơ bản nhất trong xây dựng lực lượng vũ trang nhân dân là:',ans1:'A. Giữ vững và tăng cường sự lãnh đạo của Đảng Cộng sản Việt Nam đối với lực lượng vũ trang nhân dân.',ans2:'B. Giữ vững và tăng cường sự lãnh đạo, quản lý của Nhà nước đối với lực lượng vũ trang nhân dân.',ans3:'C. Giữ vững và tăng cường sự lãnh đạo của các tổ chức chính trị trong lực lượng vũ trang.',ans4:'D. Giữ vững và tăng cường sự lãnh đạo của các cơ quan chính trị trong lực lượng vũ trang.'};quest1[71]={key:'b',q:'Đảng Cộng sản Việt Nam lãnh đạo lực lượng vũ trang nhân dân theo nguyên tắc:',ans1:'A. Tuyệt đối, trực tiếp và toàn diện.',ans2:'B. Tuyệt đối, trực tiếp về mọi mặt.',ans3:'C. Tuyệt đối, toàn diện về mọi mặt.',ans4:'D. Toàn diện trên mọi lĩnh vực.'};quest1[72]={key:'a',q:'Quan điểm, nguyên tắc cơ bản xây dựng lực lượng vũ trang nhân dân trong thời kỳ mới là:',ans1:'A. Tự lực, tự cường xây dựng lực lượng vũ trang.',ans2:'B. Tự lực cánh sinh, tăng cường đối ngoại.',ans3:'C. Phát huy nội lực, tranh thủ hợp tác kinh tế.',ans4:'D. Tích cực hợp tác quốc tế về mọi mặt.'};quest1[73]={key:'b',q:'Quan điểm, nguyên tắc cơ bản xây dựng lực lượng vũ trang nhân dân trong thời kỳ mới là:',ans1:'A. Lấy chất lượng là chính, lấy công tác huấn luyện làm cơ sở.',ans2:'B. Lấy chất lượng là chính, lấy xây dựng chính trị làm cơ sở.',ans3:'C. Lấy chất lượng huấn luyện là chính, coi trọng xây dựng chính trị.',ans4:'D. Lấy chất lượng là trọng tâm, lấy xây dựng chính trị làm trọng điểm.'};quest1[74]={key:'a',q:'Một trong những nội dung xây dựng về chính trị lực lượng vũ trang nhân dân là:',ans1:'A. Chăm lo xây dựng củng cố các tổ chức chính trị trong lực lượng vũ trang nhân dân.',ans2:'B. Chăm lo xây dựng củng cố các tổ chức chỉ huy trong lực lượng vũ trang nhân dân.',ans3:'C. Đẩy mạnh hoạt động phòng chống “diễn biến hòa bình” trong lực lượng vũ trang.',ans4:'D. Đổi mới công tác đào tạo sĩ quan trong nhà trường.'};quest1[75]={key:'d',q:'Nội dung quan trọng nhất trong xây dựng lực lượng vũ trang nhân dân về chính trị là:',ans1:'A. Phát triển số lượng Đảng viên trong lực lượng vũ trang nhân dân.',ans2:'B. Xây dựng đội ngũ, cán bộ chỉ huy trong lực lượng vũ trang nhân dân.',ans3:'C. Giáo dục chủ nghĩa Mác – Lênin, tư tưởng Hồ Chí Minh.',ans4:'D. Giữ vững nguyên tắc Đảng lãnh đạo trong lực lượng vũ trang nhân dân.'};quest1[76]={key:'b',q:'Bảo đảm lực lượng vũ trang nhân dân luôn trong tư thế sẵn sàng chiến đấu và chiến đấu thắng lợi phản ánh:',ans1:'A. Chức năng, nhiệm vụ chiến lược cơ bản, thường xuyên của lực lượng vũ trang nhân dân.',ans2:'B. Chức năng, nhiệm vụ chủ yếu cơ bản, thường xuyên của lực lượng vũ trang nhân dân.',ans3:'C. Quy luật của dân tộc Việt Nam trong lịch sử dựng nước và giữ nước.',ans4:'D. Quy luật của sự nghiệp bảo vệ Tổ quốc Việt Nam xã hội chủ nghĩa.'};quest1[77]={key:'b',q:'Phương hướng xây dựng lực lượng dự bị động viên:',ans1:'A. Hùng hậu về số lượng, có chất lượng cao, sẵn sàng động viên nhanh chóng khi cần thiết.',ans2:'B. Hùng hậu, được huấn luyện và quản lý tốt, bảo đảm khi cần thiết có thể động viên nhanh theo kế hoạch.',ans3:'C. Luôn trong tư thế sẵn sàng chiến đấu và chiến đấu thắng lợi.',ans4:'D. Luôn sẵn sàng phối hợp với lực lượng thường trực và dân quân tự vệ.'};quest1[78]={key:'a',q:'Phương hướng xây dựng dân quân tự vệ:',ans1:'A. Vững mạnh, rộng khắp, lấy chất lượng là chính.',ans2:'B. Vững mạnh, rộng khắp, lấy chất lượng chính trị là chính.',ans3:'C. Toàn diện, rộng khắp, lấy chất lượng là chính.',ans4:'D. Rộng khắp nhưng có trọng tâm trọng điểm.'};quest1[79]={key:'a',q:'Vấn đề cơ bản hàng đầu trong nhiệm vụ xây dựng quân đội, công an của Đảng trong mọi giai đoạn cách mạng là:',ans1:'A. Xây dựng quân đội, công an cách mạng.',ans2:'B. Xây dựng quân đội, công an tinh nhuệ.',ans3:'C. Xây dựng quân đội, công an chính quy.',ans4:'D. Xây dựng quân đội, công an hiện đại.'};quest1[80]={key:'a',q:'Xây dựng quân đội nhân dân, công an nhân dân chính quy là:',ans1:'A. Thực hiện thống nhất về mọi mặt (tổ chức, biên chế, trang bị).',ans2:'B. Thực hiện thống nhất về chính trị, quân sự, hậu cần.',ans3:'C. Thực hiện thống nhất về nhận thức, chính trị, tư tưởng.',ans4:'D. Thực hiện thống nhất về chính trị, mục tiêu chiến đấu.'};quest1[81]={key:'d',q:'Xây dựng quân đội nhân dân, công an nhân dân tinh nhuệ trên các lĩnh vực:',ans1:'A. Chính trị, kỹ thuật, nghiệp vụ.',ans2:'B. Chính trị, quân sự, hậu cần.',ans3:'C. Chính trị, quân sự, kỹ thuật.',ans4:'D. Chính trị, tổ chức, kỹ chiến thuật.'};quest1[82]={key:'d',q:'Một trong những biện pháp chủ yếu xây dựng lực lượng vũ trang nhân dân là:',ans1:'A. Nâng cao kết quả huấn luyện quân sự, giáo dục, đẩy mạnh đối ngoại.',ans2:'B. Nâng cao kết quả giáo dục chính trị, tư tưởng, phát triển cách đánh.',ans3:'C. Nâng cao chất lượng huấn luyện, giáo dục, xây dựng và phát triển khoa học công nghệ quốc phòng.',ans4:'D. Nâng cao chất lượng huấn luyện, giáo dục, xây dựng và phát triển khoa học quân sự, khoa học công an.'};quest1[83]={key:'c',q:'Một trong những biện pháp chủ yếu xây dựng lực lượng vũ trang nhân dân là:',ans1:'A. Từng bước giải quyết đầy đủ các yêu cầu của lực lượng vũ trang nhân dân.',ans2:'B. Từng bước giải quyết yêu cầu về tổ chức, biên chế cho lực lượng vũ trang nhân dân.',ans3:'C. Từng bước giải quyết yêu cầu về vũ khí, trang bị kỹ thuật của các lực lượng vũ trang nhân dân.',ans4:'D. Từng bước đổi mới, bổ sung đầy đủ vũ khí hiện đại cho lực lượng vũ trang nhân dân.'};quest1[84]={key:'a',q:'Một trong những biện pháp chủ yếu xây dựng lực lượng vũ trang nhân dân là:',ans1:'A. Xây dựng đội ngũ cán bộ lực lượng vũ trang nhân dân có phẩm chất, năng lực tốt.',ans2:'B. Xây dựng đội ngũ cán bộ lực lượng vũ trang nhân dân có phẩm chất tốt, số lượng đông.',ans3:'C. Xây dựng đội ngũ cán bộ lực lượng vũ trang nhân dân có số lượng đông, năng lực tốt.',ans4:'D. Xây dựng đội ngũ cán bộ lực lượng vũ trang nhân dân có số lượng đủ, phẩm chất tốt.'};quest1[85]={key:'c',q:'Cơ sở lý luận của việc kết hợp phát triển kinh tế với tăng cường củng cố quốc phòng, an ninh:',ans1:'A. Quốc phòng, an ninh tạo ra cơ sở vật chất để xây dựng kinh tế.',ans2:'B. Quốc phòng, an ninh tạo ra những biến động kích thích kinh tế phát triển.',ans3:'C. Quốc phòng, an ninh và kinh tế có quan hệ, tác động qua lại lẫn nhau.',ans4:'D. Quốc phòng, an ninh lệ thuộc hoàn toàn vào kinh tế.'};quest1[86]={key:'c',q:'Cơ sở lý luận của việc kết hợp phát triển kinh tế với tăng cường củng cố quốc phòng, an ninh:',ans1:'A. Kinh tế quyết định toàn bộ sức mạnh của quốc phòng, an ninh.',ans2:'B. Kinh tế quyết định việc đảm bảo chất lượng nguồn nhân lực cho quốc phòng, an ninh.',ans3:'C. Kinh tế quyết định đến nguồn gốc ra đời, sức mạnh của quốc phòng, an ninh.',ans4:'D. Kinh tế quyết định việc cung cấp kỹ thuật, công nghệ cho quốc phòng, an ninh.'};quest1[87]={key:'d',q:'Trong mối quan hệ giữa kinh tế xã hội với quốc phòng, an ninh thì:',ans1:'A. Kinh tế quyết định đến bản chất của quốc phòng, an ninh.',ans2:'B. Kinh tế quyết định đến tổ chức biên chế của lực lượng vũ trang.',ans3:'C. Kinh tế quyết định việc cung cấp vật chất, kỹ thuật, nhân lực cho hoạt động quốc phòng, an ninh.',ans4:'D. Tất cả đều đúng.'};quest1[88]={key:'b',q:'Kết hợp phát triển kinh tế xã hội với tăng cường củng cố quốc phòng, an ninh được xác định là:',ans1:'A. Một quy luật khách quan.',ans2:'B. Một tất yếu khách quan.',ans3:'C. Một chiến lược trọng yếu.',ans4:'D. Một nhiệm vụ chiến lược.'};quest1[89]={key:'d',q:'Kế sách “động vi binh, tĩnh vi dân” của ông cha ta có nghĩa là:',ans1:'A. Khi đất nước hòa bình, làm người lính sẵn sàng chiến đấu.',ans2:'B. Khi đất nước chiến tranh, làm người dân phát triển kinh tế.',ans3:'C. Khi đất nước có chiến tranh hoặc bình yên đều phải làm người dân phát triển kinh tế.',ans4:'D. Khi đất nước có chiến tranh thì làm người lính, đất nước bình yên thì làm người dân xây dựng kinh tế.'};quest1[90]={key:'b',q:'Trong cuộc kháng chiến chống thực dân Pháp xâm lược, Đảng ta đề ra chủ trương:',ans1:'A. Vừa kháng chiến, vừa xây dựng kinh tế.',ans2:'B. Vừa kháng chiến, vừa kiến quốc.',ans3:'C. Vừa kháng chiến, vừa tăng gia sản xuất.',ans4:'D. Vừa tăng gia sản xuất, vừa thực hành tiết kiệm.'};quest1[91]={key:'c',q:'Một trong những nội dung cơ bản của việc kết hợp phát triển kinh tế xã hội với tăng cường củng cố quốc phòng, an ninh là:',ans1:'A. Kết hợp trong chiến lược phát triển công nghiệp hóa, hiện đại hóa.',ans2:'B. Kết hợp trong chiến lược phát triển nguồn nhân lực để hiện đại hóa đất nước.',ans3:'C. Kết hợp trong xác định chiến lược phát triển kinh tế xã hội.',ans4:'D. Kết hợp trong xác định chiến lược phát triển về khoa học công nghệ.'};quest1[92]={key:'a',q:'Một trong những nội dung cơ bản của việc kết hợp phát triển kinh tế xã hội với tăng cường củng cố quốc phòng, an ninh trong phát triển các vùng lãnh thổ:',ans1:'A. Kết hợp xây dựng cơ sở kinh tế vững mạnh với xây dựng các căn cứ chiến đấu, căn cứ hậu cần kỹ thuật và hậu phương vững chắc.',ans2:'B. Kết hợp chặt chẽ giữa phát triển kinh tế xã hội với xây dựng lực lượng vũ trang.',ans3:'C. Kết hợp chặt chẽ giữa phát triển công nghiệp quốc phòng với xây dựng các thế trận phòng thủ.',ans4:'D. Kết hợp phát triển kinh tế - xã hội với xây dựng các tổ chức chính trị, đoàn thể.'};quest1[93]={key:'a',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh đối với các vùng kinh tế trọng điểm:',ans1:'A. Phải kết hợp chặt chẽ trong xây dựng kết cấu hạ tầng kinh tế với hạ tầng của nền quốc phòng toàn dân.',ans2:'B. Phát triển kinh tế phải đáp ứng phục vụ nhu cầu dân sinh và nhu cầu dự trữ quốc gia.',ans3:'C. Phát triển kinh tế phải đáp ứng phục vụ yêu cầu phòng thủ ở từng tỉnh, thành phố.',ans4:'D. Phát triển kinh tế phải đáp ứng phục vụ thỏa mãn đầy đủ nhu cầu của nhân dân.'};quest1[94]={key:'b',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh ở vùng núi, biên giới:',ans1:'A. Phải quan tâm xây dựng các tổ chức chính trị xã hội và lực lượng dân quân tự vệ thường trực.',ans2:'B. Phải quan tâm đầu tư phát triển kinh tế, củng cố quốc phòng, an ninh ở các vùng cửa khẩu, các vùng giáp biên giới với các nước.',ans3:'C. Phải quan tâm chăm lo xây dựng các tuyến biên giới giàu về kinh tế, ổn định về an ninh.',ans4:'D. Phải kết hợp chặt chẽ giữa phát triển kinh tế xã hội với xây dựng lực lượng vũ trang.'};quest1[95]={key:'a',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh ở vùng biển đảo:',ans1:'A. Có cơ chế chính sách thỏa đáng để động viên khích lệ dân ra đảo bám trụ làm ăn lâu dài.',ans2:'B. Có cơ chế chính sách thỏa đáng để động viên ngư dân đầu tư tàu thuyền đánh bắt xa bờ.',ans3:'C. Có cơ chế chính sách thỏa đáng để động viên ngư dân thành lập các tổ an ninh trên biển.',ans4:'D. Có cơ chế chính sách thỏa đáng để ngư dân tham gia xây dựng các trận địa phòng thủ.'};quest1[96]={key:'b',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh trong công nghiệp:',ans1:'A. Kết hợp ngay từ khâu quy hoạch, xây dựng các khu công nghiệp, khu chế xuất.',ans2:'B. Kết hợp ngay từ khâu quy hoạch, bố trí các đơn vị kinh tế của ngành công nghiệp.',ans3:'C. Kết hợp ngay chiến lược đào tạo nhân lực của ngành công nghiệp.',ans4:'D. Kết hợp ngày trong ý đồ bố trí mạng lưới công nghiệp quốc phòng.'};quest1[97]={key:'a',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh trong nông, lâm, ngư nghiệp:',ans1:'A. Đẩy mạnh phát triển trồng rừng gắn với công tác định canh, định cư xây dựng các cơ sở chính trị vững chắc.',ans2:'B. Đẩy mạnh khai thác, trồng rừng gắn với công tác định canh định cư xây dựng các tổ chức xã hội.',ans3:'C. Đẩy mạnh khai thác lâm sản, phát triển hệ thống giao thông, xây dựng các đoàn thể.',ans4:'D. Đẩy mạnh xóa đói giảm nghèo gắn với công tác luân chuyển dân cư, xây dựng cơ sở chính trị.'};quest1[98]={key:'c',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh trong giao thông vận tải:',ans1:'A. Xây dựng kế hoạch phát triển mạng lưới giao thông cho thời bình.',ans2:'B. Xây dựng kế hoạch động viên giao thông cho thời bình và thời chiến.',ans3:'C. Xây dựng kế hoạch động viên giao thông vận tải cho thời chiến.',ans4:'D. Xây dựng kế hoạch động viên từng giai đoạn.'};quest1[99]={key:'b',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh trong xây dựng cơ bản:',ans1:'A. Công trình trọng điểm phải tính đến yếu tố tự bảo vệ và chuyển hóa phục vụ cho quốc phòng, an ninh.',ans2:'B. Công trình nào, ở đâu đều phải tính đến yếu tố tự bảo vệ và chuyển hóa phục vụ cho quốc phòng, an ninh.',ans3:'C. Các công trình ở vùng núi, biên giới phải tính đến yếu tố tự bảo vệ và phục vụ cho quốc phòng, an ninh.',ans4:'D. Công trình trọng điểm, ở vùng kinh tế trọng điểm phải tính đến yếu tố tự bảo vệ.'};quest1[100]={key:'d',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh trong khoa học, công nghệ và giáo dục là:',ans1:'A. Coi trọng giáo dục, bồi dưỡng nhân lực, đào tạo nhân tài của đất nước.',ans2:'B. Thực hiện có hiệu quả công tác giáo dục quốc phòng, an ninh cho các đối tượng.',ans3:'C. Coi trọng đầu tư phát triển khoa học công nghệ quân sự.',ans4:'D. Cả A và B.'};quest1[101]={key:'a',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh trong y tế:',ans1:'A. Xây dựng mô hình quân dân y kết hợp trên các địa bàn, đặc biệt là ở miền núi, biên giới, hải đảo.',ans2:'B. Xây dựng mô hình quân dân y kết hợp trên các địa bàn, đặc biệt là ở các vùng kinh tế trọng điểm.',ans3:'C. Xây dựng mô hình quân dân y kết hợp trên các địa bàn, đặc biệt là ở các thành phố.',ans4:'D. Xây dựng mô hình quân dân y kết hợp trên các địa bàn, đặc biệt là ở vùng sâu, vùng xa.'};quest1[102]={key:'a',q:'Một trong những nội dung kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh trong thực hiện nhiệm vụ chiến lược bảo vệ Tổ quốc:',ans1:'A. Tổ chức biên chế lực lượng vũ trang phải phù hợp với điều kiện kinh tế và nhu cầu phòng thủ đất nước.',ans2:'B. Tổ chức biên chế lực lượng vũ trang phải phù hợp với điều kiện kinh tế và dân cư trên từng địa bàn.',ans3:'C. Tổ chức biên chế lực lượng vũ trang phải phù hợp với khả năng kinh tế của địa phương.',ans4:'D. Tổ chức biên chế lực lượng vũ trang phải phù hợp với dân số của đất nước.'};quest1[103]={key:'c',q:'Giải pháp để thực hiện phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh phải tăng cường:',ans1:'A. Sự lãnh đạo của Nhà nước, quản lý của Đảng trong thực hiện nhiệm vụ.',ans2:'B. Sự giám sát của quần chúng nhân dân và điều hành của cơ quan chuyên môn.',ans3:'C. Sự lãnh đạo của Đảng, hiệu lực quản lý nhà nước của chính quyền các cấp.',ans4:'D. Sự điều hành quản lý của Nhà nước, giám sát của nhân dân.'};quest1[104]={key:'c',q:'Đối tượng bồi dưỡng kiến thức kết hợp phát triển kinh tế - xã hội với tăng cường củng cố quốc phòng, an ninh cần tập trung vào:',ans1:'A. Học sinh, sinh viên các trường cao đẳng, đại học.',ans2:'B. Đội ngũ cán bộ các cấp từ xã, phường.',ans3:'C. Đội ngũ cán bộ chủ trì các cấp bộ, ngành, đoàn thể từ trung ương đến cơ sở.',ans4:'D. Đội ngũ cán bộ của các bộ, các ngành từ trung ương đến cơ sở.'};quest1[105]={key:'a',q:'Vì sao nước ta thường bị các thế lực ngoại xâm nhòm ngó, đe dọa, tiến công xâm lược:',ans1:'A. Việt Nam có vị trí chiến lược quan trọng ở khu vực Đông Nam Á và biển Đông.',ans2:'B. Việt Nam có dân số ít và có rất nhiều tài nguyên khoáng sản.',ans3:'C. Việt Nam có rừng vàng, biển bạc.',ans4:'D. Việt Nam là một thị trường tiềm năng.'};quest1[106]={key:'d',q:'Thời kỳ Bắc thuộc hơn 1000 năm được tính từ:',ans1:'A. Năm 179 trước Công nguyên đến năm 983.',ans2:'B. Năm 184 trước Công nguyên đến năm 938.',ans3:'C. Năm 197 trước Công nguyên đến năm 893.',ans4:'D. Năm 179 trước Công nguyên đến năm 938.'};quest1[107]={key:'d',q:'Cuộc khởi nghĩa Hai Bà Trưng vào năm:',ans1:'A. Năm 40 trước Công nguyên.',ans2:'B. Năm 140 sau Công nguyên.',ans3:'C. Năm 248 sau Công nguyên.',ans4:'D. Năm 40 sau Công nguyên.'};quest1[108]={key:'c',q:'Lý Thường Kiệt lãnh đạo cuộc kháng chiến chống quân Tống xâm lược nước ta lần thứ hai:',ans1:'A. Năm 981 – 983.',ans2:'B. Năm 1070 – 1075.',ans3:'C. Năm 1075 – 1077.',ans4:'D. Năm 1076 – 1077.'};quest1[109]={key:'d',q:'Ba lần kháng chiến chống quân Mông – Nguyên xâm lược nước ta của nhà Trần vào các năm:',ans1:'A. 1258, 1285 và 1287 đến 1289.',ans2:'B. 1258, 1284 và 1287 đến 1288.',ans3:'C. 1258, 1286 và 1287 đến 1288.',ans4:'D. 1258, 1285 và 1287 đến 1288.'};quest1[110]={key:'c',q:'Nguyên nhân làm cho cuộc kháng chiến chống quân Minh của nhà Hồ thất bại:',ans1:'A. Nhà Hồ tích cực chủ động tiến công quá mức.',ans2:'B. Nhà Hồ đã tích cực tiến công nhưng quân Minh quá mạnh.',ans3:'C. Nhà Hồ đã quá thiên về phòng thủ, dẫn đến sai lầm trong chỉ đạo chiến lược.',ans4:'D. Nhà Hồ đã không đề phòng, phòng thủ, không phản công.'};quest1[111]={key:'b',q:'Tư tưởng chỉ đạo tác chiến trong tiến hành chiến tranh của ông cha ta là:',ans1:'A. Tích cực chủ động phòng thủ.',ans2:'B. Tích cực chủ động tiến công.',ans3:'C. Kết hợp giữa tiến công và phòng ngự.',ans4:'D. Kết hợp giữa phòng ngự và tiến công.'};quest1[112]={key:'d',q:'Lý Thường Kiệt sử dụng biện pháp “tiên phát chế nhân” nghĩa là:',ans1:'A. Chuẩn bị chu đáo, chặn đánh địch từ khi mới xâm lược.',ans2:'B. Chuẩn bị thế trận phòng thủ, chống địch làm địch bị động.',ans3:'C. Chuẩn bị đầy đủ vũ khí trang bị để giành thế chủ động đánh địch.',ans4:'D. Chủ động tiến công trước để đẩy kẻ thù vào thế bị động.'};quest1[113]={key:'c',q:'Một trong những nghệ thuật đánh giặc của ông cha ta là:',ans1:'A. Lấy nhỏ đánh lớn, lấy ít địch nhiều, lấy yếu thắng mạnh.',ans2:'B. Lấy nhỏ đánh lớn, lấy ít thắng nhiều, lấy yếu thắng mạnh.',ans3:'C. Lấy nhỏ đánh lớn, lấy ít địch nhiều, lấy yếu chống mạnh.',ans4:'D. Lấy nhỏ thắng lớn, lấy ít thắng nhiều, lấy yếu chống mạnh.'};quest1[114]={key:'b',q:'Nghệ thuật lấy nhỏ đánh lớn, lấy ít địch nhiều, lấy yếu chống mạnh là sản phẩm của:',ans1:'A. Lấy kế thắng lực.',ans2:'B. Lấy thế thắng lực.',ans3:'C. Lấy mưu thắng lực.',ans4:'D. Lấy ý chí thắng lực.'};quest1[115]={key:'c',q:'Quy luật của chiến tranh là:',ans1:'A. Đông quân thì thắng, ít quân thì thua.',ans2:'B. Vũ khí hiện đại thì thắng, thô sơ thì thua.',ans3:'C. Mạnh được yếu thua.',ans4:'D. Cả A và B.'};quest1[116]={key:'a',q:'Một trong những nghệ thuật đánh giặc của ông cha ta là:',ans1:'A. Kết hợp đấu tranh giữa các mặt trận quân sự, chính trị, ngoại giao, binh vận.',ans2:'B. Kết hợp đấu tranh giữa các mặt trận quân sự, chính trị, ngoại giao, dân vận.',ans3:'C. Kết hợp đấu tranh giữa các mặt trận quân sự, chính trị, ngoại thương, dân vận.',ans4:'D. Kết hợp đấu tranh giữa các mặt trận quân sự, kinh tế, ngoại giao, binh vận.'};quest1[117]={key:'c',q:'Trong nghệ thuật kết hợp đấu tranh giữa các mặt trận thì chính trị được xác định là:',ans1:'A. Mặt trận quan trọng nhất, chủ yếu nhất.',ans2:'B. Mặt trận quyết định thắng lợi của chiến tranh.',ans3:'C. Cở sở để tạo ra sức mạnh về quân sự.',ans4:'D. Cơ sở chủ yếu để cô lập kẻ thù.'};quest1[118]={key:'a',q:'Một trong những cơ sở hình thành nghệ thuật quân sự Việt Nam từ khi có Đảng lãnh đạo:',ans1:'A. Tư tưởng quân sự Hồ Chí Minh.',ans2:'B. Từ nghệ thuật quân sự của các nước.',ans3:'C. Từ quan điểm của chủ nghĩa Mác – Lênin về đấu tranh giai cấp.',ans4:'D. Từ quan điểm của chủ nghĩa Mác – Lênin về đấu tranh dân tộc.'};quest1[119]={key:'c',q:'Trong nghệ thuật quân sự Việt Nam từ khi có Đảng lãnh đạo, nghệ thuật nào là quan trọng nhất:',ans1:'A. Nghệ thuật chiến thuật.',ans2:'B. Nghệ thuật chiến dịch.',ans3:'C. Nghệ thuật chiến lược.',ans4:'D. Nghệ thuật xác định cách đánh.'};quest1[120]={key:'a',q:'Trong nghệ thuật chiến lược quân sự của Đảng, nội dung nào là quan trọng:',ans1:'A. Xác định đúng kẻ thù, đúng đối tượng tác chiến.',ans2:'B. Xác định đúng kẻ thù, đúng đối tác.',ans3:'C. Xác định đúng đối tượng, đúng đối tác.',ans4:'D. Xác định đúng lực lượng và đối tác của ta.'};quest1[121]={key:'d',q:'Sau cách mạng tháng Tám năm 1945, Đảng ta xác định đối tượng tác chiến của quân và dân ta là:',ans1:'A. Quân đội Anh, quân đội Tưởng.',ans2:'B. Quân đội Nhật, quân đội Pháp.',ans3:'C. Quân đội Nhật, quân đội Tưởng.',ans4:'D. Quân đội Pháp xâm lược.'};quest1[122]={key:'b',q:'Khi Mỹ xâm lược Việt Nam, Đảng ta nhận định:',ans1:'A. Mỹ rất giàu và rất mạnh.',ans2:'B. Mỹ giàu nhưng không mạnh.',ans3:'C. Mỹ không giàu nhưng rất mạnh.',ans4:'D. Mỹ tuy giàu nhưng rất yếu.'};quest1[123]={key:'d',q:'Về chiến lược quân sự, chúng ta xác định thời điểm mở đầu và kết thúc chiến tranh đúng lúc là khi chúng ta:',ans1:'A. Có đủ lực lượng và vũ khí.',ans2:'B. Được quốc tế ủng hộ và giúp đỡ.',ans3:'C. Đã xây dựng được thế trận vững mạnh, lực lượng đầy đủ.',ans4:'D. Đã đáp ứng được mọi điều kiện của hoàn cảnh lịch sử.'};quest1[124]={key:'b',q:'Trong phương châm tiến hành chiến tranh được Đảng ta chỉ đạo:',ans1:'A. Tự lực cánh sinh và dựa vào các nước để đánh lâu dài.',ans2:'B. Tự lực cánh sinh, đánh lâu dài, dựa vào sức mình là chính.',ans3:'C. Tự lực cánh sinh, đánh nhanh, thắng nhanh, dựa vào sức mình là chính.',ans4:'D. Tự lực cánh sinh, đánh lâu dài, dựa vào sức mạnh thời đại.'};quest1[125]={key:'b',q:'Một số loại hình chiến dịch trong nghệ thuật quân sự Việt Nam là:',ans1:'A. Chiến dịch phục kích, tập kích, đổ bộ đường không tổng hợp.',ans2:'B. Chiến dịch tiến công, phản công, phòng ngự, phòng không, tiến công tổng hợp.',ans3:'C. Chiến dịch tiến công, tập kích đường không chiến lược.',ans4:'D. Chiến dịch tiến công đường chiến lược bằng vũ khí công nghệ cao.'};quest1[126]={key:'d',q:'Trong chiến dịch Điện Biên Phủ chúng ta đã thay đổi phương châm tác chiến đó là:',ans1:'A. Đánh lâu dài sang đánh nhanh, thắng nhanh.',ans2:'B. Đánh nhanh thắng nhanh sang đánh lâu dài.',ans3:'C. Đánh lâu dài sang đánh chắc, tiến chắc.',ans4:'D. Đánh nhanh thắng nhanh sang đánh chắc, tiến chắc.'};quest1[127]={key:'b',q:'Chiến dịch nào sau đây là chiến dịch phản công:',ans1:'A. Chiến dịch Điện Biên Phủ năm 1954.',ans2:'B. Chiến dịch Việt Bắc năm 1947.',ans3:'C. Chiến dịch Quảng Trị năm 1972.',ans4:'D. Chiến dịch Hồ Chí Minh năm 1975.'};quest1[128]={key:'b',q:'Các hình thức chiến thuật thường vận dụng trong giai đoạn đầu kháng chiến chống Pháp và chống Mỹ:',ans1:'A. Phản công, phòng ngự, tập kích.',ans2:'B. Tập kích, phục kích, vận động tiến công.',ans3:'C. Phục kích, đánh úp, đánh công kiên.',ans4:'D. Phòng ngự, phục kích, phản kích.'};quest1[129]={key:'d',q:'Bài học kinh nghiệm về nghệ thuật quân sự Việt Nam được vận dụng hiện nay:',ans1:'A. Quán triệt tư tưởng tích cực tiến công và phòng ngự.',ans2:'B. Quán triệt tư tưởng tích cực phòng ngự và chủ động phản công.',ans3:'C. Quán triệt tư tưởng tích cực phòng ngự.',ans4:'D. Quán triệt tư tưởng tích cực tiến công.'};quest1[130]={key:'c',q:'Bài học kinh nghiệm về nghệ thuật quân sự Việt Nam được vận dụng vào sự nghiệp bảo vệ Tổ quốc trong thời kỳ mới là:',ans1:'A. Tạo sức mạnh tổng hợp bằng giáo dục truyền thống.',ans2:'B. Tạo sức mạnh tổng hợp bằng xây dựng phát triển kinh tế.',ans3:'C. Tạo sức mạnh tổng hợp bằng mưu kế, thế, thời, lực.',ans4:'D. Tạo sức mạnh tổng hợp bằng thiên thời, địa lợi, nhân hòa.'};quest1[131]={key:'a',q:'Chiến dịch nào sau đây là chiến dịch tiến công:',ans1:'A. Chiến dịch Điện Biên Phủ năm 1954.',ans2:'B. Chiến dịch Việt Bắc năm 1947.',ans3:'C. Chiến dịch Quảng Trị năm 1972.',ans4:'D. Chiến dịch phòng không Hà Nội năm 1972.'};quest1[132]={key:'c',q:'Khởi nghĩa Lam Sơn diễn ra trong khoảng thời gian nào:',ans1:'A. 1418 – 1420.',ans2:'B. 1417 – 1428.',ans3:'C. 1418 – 1427.',ans4:'D. 1416 – 1428.'};/* harmony default export */ __webpack_exports__["a"] = (quest1);

