"use strict";
var quest2=[];quest2[0]={key:'a',q:'Diễn biến hòa bình là chiến lược cơ bản nhằm:',ans1:'A. Lật đổ chế độ chính trị của các nước tiến bộ, trước hết là các nước XHCN.',ans2:'B. Lật đổ chế độ kinh tế - xã hội của các nước tiến bộ, trước hết là các nước XHCN.',ans3:'C. Lật đổ chế độ chính trị cộng sản của các nước tiến bộ, trước hết là các nước XHCN.',ans4:'D. Lật đổ Đảng lãnh đạo các nước tiến bộ, trước hết là các nước XHCN.'};quest2[1]={key:'b',q:'Chiến lược “diễn biến hòa bình” do lực lượng nào tiến hành:',ans1:'A. Chủ nghĩa đế quốc và các lực lượng khủng bố.',ans2:'B. Chủ nghĩa đế quốc và các thế lực phản động.',ans3:'C. Chủ nghĩa đế quốc và các phần tử cơ hội.',ans4:'D. Chủ nghĩa đế quốc và các phần tử quá khích.'};quest2[2]={key:'a',q:'Bạo loạn lật đổ là hành động chống phá bằng:',ans1:'A. Bạo lực.',ans2:'B. Kinh tế.',ans3:'C. Chính trị.',ans4:'D. Quân sự.'};quest2[3]={key:'d',q:'Bạo loạn lật đổ gồm có những hình thức nào:',ans1:'A. Bạo loạn chính trị và bạo loạn vũ trang.',ans2:'B. Bạo loạn vũ trang kết hợp với gây rối.',ans3:'C. Kết hợp bạo loạn chính trị với vũ trang.',ans4:'D. Cả A và C đúng.'};quest2[4]={key:'d',q:'Mục tiêu nhất quán của chủ nghĩa đế quốc và các lực lượng thù địch trong sử dụng chiến lược diễn biến hòa bình đối với cách mạng Việt Nam nhằm thực hiện âm mƣu:',ans1:'A. Xóa bỏ vai trò lãnh đạo của Đảng, xóa bỏ chế độ xã hội chủ nghĩa.',ans2:'B. Lái nước ta đi theo con đường tư bản chủ nghĩa và lệ thuộc vào chủ nghĩa đế quốc.',ans3:'C. Xóa bỏ các tổ chức chính trị và buộc ta chấp nhận các điều kiện của chúng.',ans4:'D. Cả A và B đúng.'};quest2[5]={key:'a',q:'Nội dung thủ đoạn chống phá về kinh tế của chiến lược diễn biến hòa bình:',ans1:'A. Khích lệ kinh tế tư nhân phát triển, làm mất vai trò chủ đạo của kinh tế nhà nước.',ans2:'B. Khích lệ kinh tế đầu tư nước ngoài, làm mất vai trò chủ đạo của kinh tế nhà nước.',ans3:'C. Khích lệ kinh tế đầu tư trong nước phát triển, làm mất vai trò chủ đạo của kinh tế nhà nước.',ans4:'D. Khích lệ kinh tế tập thể phát triển, làm mất vai trò chủ đạo của kinh tế nhà nước.'};quest2[6]={key:'d',q:'Nội dung thủ đoạn chống phá về chính trị của chiến lược diễn biến hòa bình:',ans1:'A. Phá vỡ khối đại đoàn kết toàn dân của các tổ chức chính trị.',ans2:'B. Chia rẽ nội bộ, kích động gây rối loạn các tổ chức trong xã hội.',ans3:'C. Kích động đòi cô lập Đảng, Nhà nước với quân đội nhân dân.',ans4:'D. Kích động đòi thực hiện chế độ đa nguyên chính trị, đa đảng đối lập.'};quest2[7]={key:'b',q:'Mục đích của thủ đoạn chống phá về tư tưởng trong chiến lược diễn biến hòa bình:',ans1:'A. Xóa bỏ hệ tư tưởng của Nhà nước xã hội chủ nghĩa.',ans2:'B. Xóa bỏ chủ nghĩa Mác – Lênin, tư tưởng Hồ Chí Minh.',ans3:'C. Xóa bỏ vai trò quản lý điều hành của Nhà nước xã hội chủ nghĩa.',ans4:'D. Xóa bỏ vai trò lãnh đạo của các tổ chức chính trị xã hội.'};quest2[8]={key:'d',q:'Thực hiện thủ đoạn chống phá ta về văn hóa, kẻ thù tập trung:',ans1:'A. Phá vỡ truyền thống, kinh nghiệm của văn hóa Việt Nam.',ans2:'B. Xuyên tạc, bôi nhọ truyền thống văn hóa quý báu của chúng ta.',ans3:'C. Phủ nhận các quan điềm, chính sách văn hóa của Đảng và Nhà nước.',ans4:'D. Làm phai mờ bản sắc văn hóa và giá trị văn hóa của dân tộc Việt Nam.'};quest2[9]={key:'b',q:'Nội dung kẻ thù lợi dụng để chống phá cách mạng Việt Nam về vấn đề dân tộc là:',ans1:'A. Lợi dụng các mâu thuẫn nội bộ trong đồng bào dân tộc để kích động bạo loạn.',ans2:'B. Lợi dụng những khó khăn ở vùng đồng bào dân tộc ít người và những tồn tại do lịch sử để lại.',ans3:'C. Lợi dụng các mâu thuẫn giữa các dân tộc do nhiều nguyên nhân gây ra.',ans4:'D. Lợi dụng các hủ tục lạc hậu của đồng bào dân tộc để kích động.'};quest2[10]={key:'c',q:'Các thế lực thù địch triệt để lợi dụng chính sách tự do tôn giáo của Đảng, Nhà nước ta để:',ans1:'A. Truyền bá mê tin dị đoan và tư tưởng phản động chủ nghĩa xã hội.',ans2:'B. Truyền đạo trái phép nhằm thực hiện âm mưu tôn giáo hóa dân tộc.',ans3:'C. Truyền bá mê tín dị đoan, tập hợp lực lượng để chống phá cách mạng.',ans4:'D. Truyền bá mê tín và tổ chức lực lượng tiến hành khủng bố.'};quest2[11]={key:'d',q:'Thực hiện thủ đoạn chống phá trên lĩnh vực quốc phòng – an ninh nhằm:',ans1:'A. Mua chuộc cán bộ cao cấp của quân đội và sĩ quan trong lực lượng vũ trang.',ans2:'B. Phủ nhận vai trò quốc phòng an ninh trong sự nghiệp đổi mới đất nước.',ans3:'C. Phủ nhận vai trò lãnh đạo của Đảng trong lĩnh vực quốc phòng an ninh và đối với LLVT.',ans4:'D. Chia rẽ gây mất đoàn kết quân đội, công an, dân quân tự vệ và bộ đội biên phòng.'};quest2[12]={key:'b',q:'Thủ đoạn chống phá trên lĩnh vực đối ngoại trong chiến lược “diễn biến hòa bình” nhằm chia rẽ:',ans1:'A. Việt Nam với các nước xã hội chủ nghĩa và các nước tiến bộ.',ans2:'B. Việt Nam với Lào và các nước xã hội chủ nghĩa.',ans3:'C. Việt Nam với Campuchia và các nước tiến bộ.',ans4:'D. Việt Nam với Lào, Campuchia và các nước XHCN.'};quest2[13]={key:'b',q:'Trong quá trình bạo loạn, bọn phản động tìm mọi cách để:',ans1:'A. Mở rộng lực lượng trong và ngoài nước liên hiệp bằng quân sự.',ans2:'B. Mở rộng phạm vi, quy mô, lực lượng, kêu gọi tài trợ của nước ngoài.',ans3:'C. Mở rộng phạm vi, quy mô lực lượng, đập phá trụ sở, uy hiếp chính quyền địa phương.',ans4:'D. Mở rộng phạm vi, quy mô lực lượng, đập phá trụ sở, lật đổ chính quyền địa phương.'};quest2[14]={key:'a',q:'Nguyên tắc xử lý khi có bạo loạn diễn ra là:',ans1:'A. Nhanh gọn, kiên quyết, linh hoạt, đúng đối tượng, không để lan rộng kéo dài.',ans2:'B. Nhanh gọn, kiên quyết, triệt để, đúng đối tượng, không để lan rộng kéo dài.',ans3:'C. Nhanh gọn, linh hoạt, khôn khéo, đúng đối tượng, không để lan rộng kéo dài.',ans4:'D. Nhanh gọn, linh hoạt, mềm dẻo, đúng đối tượng, không để lan rộng kéo dài.'};quest2[15]={key:'d',q:'Mục tiêu phòng chống chiến lược “diễn biến hòa bình” là:',ans1:'A. Giữ vững ổn định chính trị xã hội của đất nước, tạo môi trường hòa bình để đẩy mạnh công nghiệp hóa, hiện đại hóa.',ans2:'B. Bảo vệ vững chắc độc lập, chủ quyền thống nhất và toàn vẹn lãnh thổ của Tổ quốc.',ans3:'C. Bảo vệ an ninh quốc gia, trật tự an toàn xã hội và nền văn hóa sự nghiệp đổi mới và lợi ích quốc giadân tộc.',ans4:'D. Tất cả đều đúng.'};quest2[16]={key:'a',q:'Nhiệm vụ phòng chống chiến lược “diễn biến hòa bình” và bạo loạn lật đổ được xác định:',ans1:'A. Là nhiệm vụ cấp bách, hàng đầu trong các nhiệm vụ quốc phòng an ninh hiện nay.',ans2:'B. Là nhiệm vụ cấp bách, trọng tâm trong các nhiệm vụ quốc phòng an ninh hiện nay.',ans3:'C. Là nhiệm vụ cấp bách, quan trọng trong các nhiệm vụ quốc phòng an ninh hiện nay.',ans4:'D. Là nhiệm vụ trọng tâm, hàng đầu của sự nghiệp cách mạng nước ta hiện nay.'};quest2[17]={key:'c',q:'Quan điểm chủ đạo trong đấu tranh phòng chống chiến lược diễn biến hòa bình là:',ans1:'A. Là một cuộc đấu tranh dân tộc rất gay go, quyết liệt trong mọi lĩnh vực.',ans2:'B. Là một cuộc đấu tranh giai cấp gay go, quyết liệt, lâu dài và phức tạp trên mọi lĩnh vực.',ans3:'C. Là một cuộc đấu tranh giai cấp, dân tộc gay go, quyết liệt, lâu dài và phức tạp trên mọi lĩnh vực.',ans4:'D. Là một cuộc chiến tranh chính trị quyết liệt giữa chủ nghĩa tư bản và CNXH.'};quest2[18]={key:'c',q:'Trong phòng chống chiến lược “diễn biến hòa bình” và bạo loạn lật đổ cần phát huy sức mạnh tổng hợp:',ans1:'A. Của khối đại đoàn kết toàn dân, của cả hệ thống chính trị, dưới sự lãnh đạo của Đảng.',ans2:'B. Của lực lượng vũ trang nhân dân Việt Nam, dưới sự lãnh đạo của Đảng.',ans3:'C. Của các lĩnh vực, các mặt trận, của cả nước, dưới sự lãnh đạo của Đảng.',ans4:'D. Của các tổ chức chính trị xã hội, các tổ chức quần chúng, dưới sự lãnh đạo của Đảng.'};quest2[19]={key:'a',q:'Giải pháp phòng chống chiến lược diễn biến hòa bình, bạo loạn lật đổ là:',ans1:'A. Nâng cao nhận thức về âm mưu phá hoại của kẻ thù.',ans2:'B. Xây dựng ý thức bảo vệ Tổ quốc cho toàn dân.',ans3:'C. Xây dựng các tổ chức chính trị vững mạnh.',ans4:'D. Xây dựng lực lượng quân đội, công an vững mạnh.'};quest2[20]={key:'b',q:'Giải pháp phòng chống chiến lược diễn biến hòa bình, bạo loạn lật đổ là:',ans1:'A. Đẩy mạnh công nghiệp hóa, hiện đại hóa, chống nguy cơ tụt hậu về kinh tế.',ans2:'B. Xây dựng cơ sở chính trị - xã hội vững mạnh về mọi mặt.',ans3:'C. Đẩy lùi các hủ tục lạc hậu, mê tín dị đoan và các tệ nạn xã hội.',ans4:'D. Xây dựng lòng yêu nước cho tuổi trẻ, nhất là học sinh sinh viên.'};quest2[21]={key:'c',q:'Giải pháp phòng chống chiến lược diễn biến hòa bình, bạo loạn lật đổ là:',ans1:'A. Tăng cường quan hệ đối ngoại với các nước, các chế độ chính trị trên thế giới.',ans2:'B. Tăng cường quan hệ với các nước XHCN và các Đảng Cộng sản trên thế giới.',ans3:'C. Xây dựng, luyện tập các phương án, các tình huống chống diễn biến hòa bình, bạo loạn lật đổ.',ans4:'D. Xây dựng các lực lượng chuyên trách để phòng chống diễn biến hòa bình, bạo loạn lật đổ.'};quest2[22]={key:'d',q:'Vũ khí công nghệ cao là loại vũ khí:',ans1:'A. Dựa trên những thành tựu của cách mạng khoa học.',ans2:'B. Dựa trên sự phát triển của nền khoa học quân sự.',ans3:'C. Dựa trên sự phát triển của nền khoa học tiên tiến.',ans4:'D. Có sự nhảy vọt về chất lượng về tính năng kỹ thuật, chiến thuật.'};quest2[23]={key:'d',q:'Đặc điểm của vũ khí công nghệ cao:',ans1:'A. Khả năng phá hủy được nhiều phương tiện của đối phương.',ans2:'B. Khả năng tiêu diệt được nhiều sinh lực của đối phương.',ans3:'C. Khả năng phát triển và cạnh tranh cao.',ans4:'D. Hàm lượng tri thức, kỹ năng tự động hóa cao.'};quest2[24]={key:'a',q:'Tiến công hỏa lực bằng vũ khí công nghệ cao:',ans1:'A. Phương thức tiến hành chiến tranh kiểu mới.',ans2:'B. Hình thức tiến hành chiến tranh kiểu mới.',ans3:'C. Hình thức chiến thuật kiểu mới.',ans4:'D. Thủ đoạn tác chiến mới.'};quest2[25]={key:'a',q:'Mục đích sử dụng vũ khí công nghệ cao của địch nhằm:',ans1:'A. Giành quyền làm chủ trên không, làm chủ chiến trường, phá hoại tiềm lực kinh tế, quốc phòng.',ans2:'B. Giành quyền làm chủ trên biển, làm chủ chiến trường, phá hoại tiềm lực kinh tế quốc phòng.',ans3:'C. Giành quyền làm chủ trên bộ, làm chủ chiến trường, phá hoại tiềm lực kinh tế quốc phòng.',ans4:'D. Giành quyền làm chủ địa hình, làm chủ chiến trường, phá hoại tiềm lực kinh tế quốc phòng.'};quest2[26]={key:'d',q:'Hƣớng tiến công tác chiến vũ khí công nghệ cao có thể xuất phát từ:',ans1:'A. Biên giới trên bộ, chính diện, trong chiều sâu cả nước.',ans2:'B. Biên giới trên biển, nhiệt độ cao, cường độ lớn.',ans3:'C. Biên giới trên không, ngày từ đầu và suốt quá trình chiến tranh.',ans4:'D. Từ nhiều hướng, vào nhiều mục tiêu và diễn ra cùng một lúc.'};quest2[27]={key:'b',q:'Trong chiến tranh Nam tư 1999 địch đã sử dụng:',ans1:'A. 100% vũ khí công nghệ cao.',ans2:'B. 90% vũ khí công nghệ cao.',ans3:'C. 50% vũ khí công nghệ cao.',ans4:'D. 30% vũ khí công nghệ cao.'};quest2[28]={key:'a',q:'Trong cuộc chiến tranh xâm lược Việt Nam, Mỹ đã sử dụng vũ khí công nghệ cao chƣa:',ans1:'A. Đã sử dụng.',ans2:'B. Chưa sử dụng.',ans3:'C. Chuẩn bị sử dụng.',ans4:'D. Có kế hoạch sử dụng.'};quest2[29]={key:'a',q:'Điểm mạnh của vũ khí công nghệ cao là:',ans1:'A. Độ chính xác cao, uy lực sát thương lớn, tầm hoạt động xa.',ans2:'B. Hàm lượng tri thức cao, được nâng cấp liên tuc.',ans3:'C. Có tính cạnh tranh cao, hiệu suất chiến đấu lớn.',ans4:'D. Không bị tác động bởi địa hình thời tiết.'};quest2[30]={key:'c',q:'Điểm yếu của vũ khí công nghệ cao:',ans1:'A. Bay ở tầm thấp và tốc độ chậm dễ bị đối phương theo dõi phát hiện.',ans2:'B. Uy lực sát thương quá lớn nên bị thế giới lên án.',ans3:'C. Dựa hoàn toàn vào các phương tiện kỹ thuật, dễ bị đối phương đánh lừa.',ans4:'D. Gặp địa hình rừng núi không phát huy được tác dụng.'};quest2[31]={key:'c',q:'Vì sao tác chiến bằng vũ khí công nghệ cao không thể kéo dài:',ans1:'A. Vì công tác bảo quản, bảo dưỡng quá khó khăn.',ans2:'B. Vì chế tạo quá phức tạp, khó đảm bảo số lượng.',ans3:'C. Vì quá tốn kém.',ans4:'D. Vì sợ dư luận quốc tế.'};quest2[32]={key:'d',q:'Tổ chức nghi binh đánh lừa vũ khí công nghệ cao của địch là:',ans1:'A. Lợi dụng môi trường tự nhiên như địa hình, địa vật, rừng che giấu mục tiêu.',ans2:'B. Sử dụng các biện pháp kỹ thuật giảm bớt đặc trưng ánh sáng, âm thanh, điện từ.',ans3:'C. Làm cho mục tiêu của ta gần giống như môi trường xung quanh.',ans4:'D. Hành động tạo hiện trường giả để đánh lừa đối phương.'};quest2[33]={key:'b',q:'Biện pháp thụ động nhằm phòng chống địch tiến công hỏa lực bằng vũ khí công nghệ cao:',ans1:'A. Tổ chức phá hoại hệ thống trinh sát, thông tin, rađa của địch.',ans2:'B. Tổ chức bố trí lực lượng phân tán, có khả năng tác chiến độc lập.',ans3:'C. Nắm chắc thời cơ, cơ động phòng tránh, chủ động đánh địch từ xa.',ans4:'D. Đánh vào mắc xích then chốt của hệ thống vũ khí công nghệ cao.'};quest2[34]={key:'a',q:'Biện pháp chủ động phòng chống địch tiến công bằng vũ khí công nghệ cao:',ans1:'A. Gây nhiễu các trang bị trinh sát của địch làm giảm hiệu quả trinh sát.',ans2:'B. Nắm chắc thời cơ, trinh sát chặt chẽ, chủ động đánh địch từ xa.',ans3:'C. Tổ chức bố trí lực lượng phân tán, đánh vào mắc xích then chốt.',ans4:'D. Dụ địch đánh vào những mục tiêu có giá trị thấp làm chúng tiêu hao lớn.'};quest2[35]={key:'d',q:'Biện pháp chủ động nhằm phòng chống địch tiến công hỏa lực bằng vũ khí công nghệ cao:',ans1:'A. Che giấu mục tiêu làm hạn chế đặc trưng của mục tiêu ban đầu.',ans2:'B. Tổ chức, bố trí lực lượng phân tán, có khả năng tác chiến độc lập.',ans3:'C. Tổ chức tốt việc nghi binh đánh lừa địch.',ans4:'D. Nắm chắc thời cơ chủ động đánh địch từ xa, phá thế tiến công của địch.'};quest2[36]={key:'c',q:'Biện pháp chủ động nhằm phòng chống địch tiến công hỏa lực bằng vũ khí công nghệcao:',ans1:'A. Lợi dụng đặc điểm đồng bộ của phương tiện trinh sát của địch để đánh địch.',ans2:'B. Lợi dụng đặc điểm đồng bộ của phương tiện trinh sát của địch để che giấu mục tiêu.',ans3:'C. Lợi dụng đặc điểm đồng bộ của vũ khí công nghệ cao, đánh vào mắc xích then chốt.',ans4:'D. Tất cả đều đúng.'};quest2[37]={key:'c',q:'Biện pháp chủ động nhằm phòng chống địch tiến công hỏa lực bằng vũ khí công nghệ cao:',ans1:'A. Dụ địch đánh vào những mục tiêu có giá trị thấp làm chúng tiêu hao lớn.',ans2:'B. Tổ chức bố trí lực lượng phân tán, có khả năng tác chiến, thụ động.',ans3:'C. Cơ động phòng tránh nhanh, đánh trả kịp thời chính xác.',ans4:'D. Xây dựng hầm ngầm để tằng cường khả năng phòng thủ.'};quest2[38]={key:'d',q:'Trong chiến tranh, để phòng chống trinh sát của địch, trước tiên cần xác định:',ans1:'A. Hạn chế đặc trưng mục tiêu, xóa bỏ sự khác biệt giữa mục tiêu với môi trường.',ans2:'B. Che giấu mục tiêu, triệt để lợi dụng môi trường tự nhiên.',ans3:'C. Ngụy trang mục tiêu, gây nhiễu phương tiện trinh sát của địch.',ans4:'D. Ý thức phòng chống trinh sát, sau đó áp dụng các biện pháp phòng chống.'};quest2[39]={key:'d',q:'Về mặt tư tưởng, hiểu đúng đặc điểm của vũ khí công nghệ cao nhằm:',ans1:'A. Tổ chức lực lượng, phương tiện đánh phá vũ khí công nghệ cao hiệu quả.',ans2:'B. Tổ chức các phương án phòng tránh đánh trả tốt nhất.',ans3:'C. Khoét sâu điểm yếu làm cho vũ khí công nghệ cao bị mất tác dụng.',ans4:'D. Không tuyệt đối hóa dẫn đến tâm lý hoang mang, không coi thường dẫn đến chủ quan.'};quest2[40]={key:'d',q:'Lực lượng tự vệ được tổ chức ở:',ans1:'A. Ở xã, phường thị trấn.',ans2:'B. Ở cơ quan nhà nước, tổ chức chính trị.',ans3:'C. Tổ chức chính trị xã hội, đơn vị sự nghiệp, tổ chức kinh tế.',ans4:'D. Cả B và C.'};quest2[41]={key:'a',q:'Vị trí, vai trò của lực lượng dân quân tự vệ:',ans1:'A. Dân quân tự vệ là lực lượng cơ bản trong xây dựng nền quốc phòng toàn dân.',ans2:'B. Dân quân tự vệ là một lực lượng chiến lược trong chiến tranh nhân dân, quốc phòng toàn dân.',ans3:'C. Dân quân tự vệ là lực lượng xung kích trong xây dựng nền quốc phòng toàn dân.',ans4:'D. Dân quân tự vệ là lực lượng xung kích trong xây dựng nền quốc phòng toàn dân và an ninh nhân dân.'};quest2[42]={key:'b',q:'Nhiệm vụ của lực lượng dân quân tự vệ:',ans1:'A. Học tập chính trị và huấn luyện quân sự.',ans2:'B. Học tập chính trị, pháp luật, huấn luyện quân sự và diễn tập.',ans3:'C. Thực hiện các nhiệm vụ khác theo quy định của pháp luật.',ans4:'D. Cả B và C.'};quest2[43]={key:'d',q:'Phương châm xây dựng lực lượng dân quân tự vệ:',ans1:'A. Vững mạnh, rộng khắp coi trọng chất lượng là chính.',ans2:'B. Vững mạnh, toàn diện lấy chất lượng chính trị là chính.',ans3:'C. Vững mạnh, coi trọng cả số lượng và chất lượng.',ans4:'D. Vững mạnh về mọi mặt, lấy chính trị làm cơ sở.'};quest2[44]={key:'c',q:'Dân quân tự vệ được tổ chức thành 2 lực lượng là:',ans1:'A. Lực lượng cơ động và lực lượng rộng rãi.',ans2:'B. Lực lượng nòng cốt và lực lượng rộng rãi.',ans3:'C. Lực lượng quân sự và lực lượng chính trị.',ans4:'D. Lực lượng cơ động và lực lượng dự bị.'};quest2[45]={key:'d',q:'Nhiệm vụ của lực lượng dân quân tự vệ cơ động:',ans1:'A. Trực tiếp chiến đấu, phục vụ chiến đấu.',ans2:'B. Cơ động chiến đấu, chiến đấu tại chỗ.',ans3:'C. Chiến đấu, chi viện cho lực lượng quân đội và công an khi cần.',ans4:'D. Chiến đấu, chi viện cho lực lượng chiến đấu tại chỗ và địa phương khác khi cần.'};quest2[46]={key:'c',q:'Khi hết thời gian phục vụ trong lực lượng dân quân tự vệ nòng cốt thì có thể tham gia:',ans1:'A. Lực lượng dân quân tự vệ cơ động.',ans2:'B. Lực lượng dân quân tự vệ tại chỗ.',ans3:'C. Lực lượng dân quân tự vệ rộng rãi.',ans4:'D. Lực lượng dân quân tự vệ thường trực.'};quest2[47]={key:'a',q:'Độ tuổi của công dân Việt Nam tham gia lực lượng dân quân tự vệ:',ans1:'A. Nam từ đủ 18 tuổi đến hết 30 tuổi; nữ từ đủ 18 tuổi đến hết 35 tuổi.',ans2:'B. Nam từ đủ 18 tuổi đến hết 35 tuổi; nữ từ đủ 18 tuổi đến hết 35 tuổi.',ans3:'C. Nam từ đủ 18 tuổi đến hết 40 tuổi; nữ từ đủ 18 tuổi đến hết 35 tuổi.',ans4:'D. Nam từ đủ 18 tuổi đến hết 45 tuổi; nữ từ đủ 18 tuổi đến hết 40 tuổi.'};quest2[48]={key:'c',q:'Tiểu đội trƣởng, trung đội trƣởng dân quân tự vệ do ai bổ nhiệm:',ans1:'A. Chỉ huy trưởng Ban chỉ huy quân sự xã bổ nhiệm.',ans2:'B. Chủ tịch ủy ban nhân dân xã bổ nhiệm.',ans3:'C. Huyện đội trưởng bổ nhiệm.',ans4:'D. Chủ tịch ủy ban nhân dân huyện bổ nhiệm.'};quest2[49]={key:'a',q:'Trong ban chỉ huy quân sự, chính trị viên do ai đảm nhiệm:',ans1:'A. Bí thư Chi bộ hoặc Bí thư Đảng ủy chủ nhiệm.',ans2:'B. Phó bí thư, chủ tịch ủy ban nhân dân đảm nhiệm.',ans3:'C. Ủy viên thường vụ Đảng ủy đảm nhiệm.',ans4:'D. Chủ tịch ủy ban nhân dân đảm nhiệm.'};quest2[50]={key:'c',q:'Biện pháp xây dựng lực lượng dân quân tự vệ trong giai đoạn hiện nay:',ans1:'A. Phát huy sức mạnh tổng hợp của các tổ chức, các lực lượng.',ans2:'B. Phát huy sức mạnh tổng hợp của chính quyền địa phương.',ans3:'C. Phát huy sức mạnh tổng hợp trên địa bàn.',ans4:'D. Phát huy sức mạnh tổng hợp của nhân dân trên địa bàn.'};quest2[51]={key:'b',q:'Lực lượng dự bị động viên bao gồm:',ans1:'A. Quân nhân thường trực và phương tiện kỹ thuật.',ans2:'B. Quân nhân dự bị và phương tiện kỹ thuật.',ans3:'C. Quân nhân chuyên nghiệp và nhân viên kỹ thuật.',ans4:'D. Sĩ quan và hạ sĩ quan quân đội, công an.'};quest2[52]={key:'a',q:'Công tác xây dựng lực lượng dự bị động viên có vị trí vai trò:',ans1:'A. Rất quan trọng trong chiến lược bảo vệ Tổ quốc xã hội chủ nghĩa.',ans2:'B. Quan trọng trong chiến lược bảo vệ Tổ quốc xã hội chủ nghĩa.',ans3:'C. Trọng tâm trong chiến lược bảo vệ Tổ quốc xã hội chủ nghĩa.',ans4:'D. Cấp bách trong chiến lược bảo vệ Tổ quốc xã hội chủ nghĩa.'};quest2[53]={key:'c',q:'Quan điểm, nguyên tắc xây dựng lực lượng dự bị động viên:',ans1:'A. Đảm bảo số lượng, chất lượng cao, rộng khắp, sẵn sàng chiến đấu.',ans2:'B. Đảm bảo toàn diện nhưng có trọng tâm, trọng điểm, tập trung nâng cao chất lượng.',ans3:'C. Đảm bảo số lượng đủ, chất lượng cao, xây dựng toàn diện nhưng có trọng tâm trọng điểm.',ans4:'D. Đảm bảo số lượng đông, chất lượng cao cho những đơn vị làm nhiệm vụ sẵn sàng chiến đấu.'};quest2[54]={key:'d',q:'Quan điểm, nguyên tắc xây dựng lực lượng dự bị động viên là phải:',ans1:'A. Phát huy sức mạnh của toàn dân trên tất cả các lĩnh vực hoạt động xã hội.',ans2:'B. Xây dựng lực lượng dự bị động viên hùng mạnh, sẵn sàng chiến đấu.',ans3:'C. Phát huy sức mạnh của bộ, ngành và địa phương.',ans4:'D. Phát huy sức mạnh tổng hợp của cả hệ thống chính trị.'};quest2[55]={key:'b',q:'Quan điểm, nguyên tắc xây dựng lực lượng dự bị động viên:',ans1:'A. Đặt dưới sự lãnh đạo của Bộ Quốc Phòng và Ủy ban nhân dân các địa phương.',ans2:'B. Đặt dưới sự lãnh đạo của Đảng các cấp ở địa phương, bộ, ngành.',ans3:'C. Đặt dưới sự lãnh đạo của các tổ chức chính trị xã hội.',ans4:'D. Đặt dưới sự lãnh đạo của Ủy ban nhân dân các cấp.'};quest2[56]={key:'a',q:'Xây dựng lực lượng dự bị động viên đặt dƣới sự lãnh đạo:',ans1:'A. Tuyệt đối, trực tiếp về mọi mặt của Đảng.',ans2:'B. Tuyệt đối, trực tiếp về mọi mặt của Nhà nước.',ans3:'C. Tuyệt đối, trực tiếp về mọi mặt của Bộ Quốc Phòng.',ans4:'D. Toàn diện về mọi mặt của các tổ chức xã hội.'};quest2[57]={key:'a',q:'Nội dung xây dựng lực lượng dự bị động viên:',ans1:'A. Tạo nguồn, đăng ký, quản lý lực lượng dự bị động viên.',ans2:'B. Tạo nguồn, đăng ký, biên chế lực lượng dự bị động viên.',ans3:'C. Tạo nguồn, đăng ký, tổ chức lực lượng dự bị động viên theo kế hoạch.',ans4:'D. Tạo nguồn, đăng ký, kiểm tra lực lượng dự bị động viên theo quy định.'};quest2[58]={key:'b',q:'Nguyên tắc sắp xếp quân nhân dự bị vào các đơn vị dự bị động viên là:',ans1:'A. Theo mức độ sức khỏe, theo tuổi đời và theo cư trú.',ans2:'B. Theo trình độ chuyên môn, theo hạng, theo cư trú.',ans3:'C. Theo quân hàm, theo chức vụ và chuyên môn.',ans4:'D. Theo hạng và theo trình độ văn hóa.'};quest2[59]={key:'d',q:'Phương châm huấn luyện đối với lực lượng dự bị động viên:',ans1:'A. Chất lượng, thiết thực, hiệu quả tập trung vào khoa học quân sự hiện đại.',ans2:'B. Cơ bản thống nhất, coi trọng khâu kỹ thuật tác chiến, phối hợp giữa các lực lượng.',ans3:'C. Chất lượng, thiết thực, hiệu quả sát thực tế chiến đấu tại địa bàn.',ans4:'D. Chất lượng, thiết thực, hiệu quả, tập trung có trọng tâm, trọng điểm.'};quest2[60]={key:'d',q:'Xây dựng lực lượng dự bị động viên là nhiệm vụ chính trị thường xuyên của:',ans1:'A. Nhà nước, Ủy ban nhân dân các cấp.',ans2:'B. Cấp ủy, chính quyền địa phương.',ans3:'C. Cán bộ, ngành, đoàn thể từ trung ương đến địa phương.',ans4:'D. Toàn Đảng, toàn dân, toàn quân và cả hệ thống chính trị.'};quest2[61]={key:'a',q:'Động viên công nghiệp Quốc phòng được chuẩn bị:',ans1:'A. Từ thời bình.',ans2:'B. Khi bắt đầu chiến tranh.',ans3:'C. Trong quá trình chiến tranh.',ans4:'D. Kết thúc chiến tranh.'};quest2[62]={key:'b',q:'Động viên công nghiệp có áp dụng đối với các doanh nghiệp có vốn đầu tư nước ngoàitại Việt Nam hay không:',ans1:'A. Có.',ans2:'B. Không.',ans3:'C. Tùy tình hình cụ thể.',ans4:'D. Có thể động viên một phần hoặc toàn bộ.'};quest2[63]={key:'d',q:'Quyết định và thông báo quyết định động viên công nghiệp quốc phòng do:',ans1:'A. Chủ tịch Quốc hội quy định.',ans2:'B. Chủ tịch nước quy định.',ans3:'C. Bộ trưởng Bộ quốc phòng quy định.',ans4:'D. Chính phủ quy định.'};quest2[64]={key:'c',q:'Các yếu tố cấu thành quốc gia:',ans1:'A. Lãnh thổ, dân cư và nhà nước.',ans2:'B. Lãnh thổ, dân cư và chế độ chính trị.',ans3:'C. Lãnh thổ, dân cư và quyền lực công cộng.',ans4:'D. Lãnh thổ, dân cư và hệ thống chính trị.'};quest2[65]={key:'c',q:'Lãnh thổ quốc gia là:',ans1:'A. Phạm vi không gian bao gồm vùng đất, vùng biển, vùng trời của quốc gia.',ans2:'B. Phạm vi không gian bao gồm vùng đất, các đảo, vùng biển, vùng trời của quốc gia.',ans3:'C. Phạm vi không gian được giới hạn bởi biên giới quốc gia thuộc chủ quyền hoàn toàn và đầy đủcủa một quốc gia.',ans4:'D. Phạm vi không gian bao gồm vùng đất, vùng biển, vùng trời, các đảo và quần đảo của quốc gia.'};quest2[66]={key:'c',q:'Lãnh thổ quốc gia Việt Nam bao gồm:',ans1:'A. Vùng đất, vùng biển, vùng trời quốc gia.',ans2:'B. Đất liền, vùng biển đảo và vùng trời quốc gia.',ans3:'C. Vùng đất, vùng biển, vùng trời quốc gia và lãnh thổ quốc gia đặc biệt.',ans4:'D. Vùng đất, vùng biển đảo, vùng trời và khu vực biên giới quốc gia.'};quest2[67]={key:'a',q:'Vùng đất quốc gia Việt Nam bao gồm:',ans1:'A. Đất liền, các đảo và quần đảo.',ans2:'B. Đất liền, các đảo và bán đảo.',ans3:'C. Đất liền và các quần đảo.',ans4:'D. Đất liền, bán đảo và quần đảo.'};quest2[68]={key:'b',q:'Trong vùng biển quốc gia, nội thủy là:',ans1:'A. Vùng nước nằm bên trong lục địa.',ans2:'B. Vùng biển nằm ở phía trong của đường cơ sở.',ans3:'C. Vùng biển nằm phía trong biên giới quốc gia trên biển.',ans4:'D. Vùng biển nằm phía ngoài đường cơ sở.'};quest2[69]={key:'b',q:'Trong vùng biển quốc gia, nội thủy có chế độ pháp lý:',ans1:'A. Tương tự như lãnh hải.',ans2:'B. Như trên đất liền.',ans3:'C. Như vùng đặc quyền kinh tế.',ans4:'D. Như vùng tiếp giáp lãnh hải.'};quest2[70]={key:'b',q:'Quốc gia ven biển có chủ quyền hoàn toàn, đầy đủ và tuyệt đối ở:',ans1:'A. Lãnh hải.',ans2:'B. Nội thủy.',ans3:'C. Vùng tiếp giáp lãnh hải.',ans4:'D. Vùng đặc quyền kinh tế.'};quest2[71]={key:'d',q:'Vùng biển quốc gia, lãnh hải là:',ans1:'A. Vùng biển có chiều rộng 12 hải lý tính từ bờ biển.',ans2:'B. Vùng biển có chiều rộng 12 hải lý tính từ biên giới quốc gia trên biển.',ans3:'C. Vùng biển có chiều rộng 10 hải lý tính từ đường cơ sở.',ans4:'D. Vùng biển có chiều rộng 12 hải lý tính từ đường cơ sở.'};quest2[72]={key:'b',q:'Vùng biển nào, quốc gia ven biển có chủ quyền hoàn toàn, đầy đủ và không tuyệt đối:',ans1:'A. Nội thủy.',ans2:'B. Lãnh hải.',ans3:'C. Vùng tiếp giáp lãnh hải.',ans4:'D. Vùng đặc quyền kinh tế.'};quest2[73]={key:'d',q:'Lãnh hải của nước ta gồm:',ans1:'A. Lãnh hải của đất liền.',ans2:'B. Lãnh hải của đảo.',ans3:'C. Lãnh hải của quần đảo.',ans4:'D. Cả ba lựa chọn trên.'};quest2[74]={key:'a',q:'Biên giới quốc gia trên biển là:',ans1:'A. Ranh giới ngoài của lãnh hải.',ans2:'B. Ranh giới ngoài của các vùng tiếp giáp lãnh hải.',ans3:'C. Ranh giới ngoài của vùng đặc quyền kinh tế.',ans4:'D. Ranh giới ngoài của nội thủy.'};quest2[75]={key:'c',q:'Vùng trời quốc gia là:',ans1:'A. Khoảng không gian phía trên vùng đất và vùng biển quốc gia.',ans2:'B. Khoảng không gian phía trên đất liền và vùng biển quốc gia.',ans3:'C. Khoảng không gian phía trên lãnh thổ quốc gia.',ans4:'D. Khoảng không gian phía trên đất liền, đảo và quần đảo.'};quest2[76]={key:'d',q:'Chủ quyền quốc gia là:',ans1:'A. Quyền làm chủ một cách độc lập, toàn vẹn và đầy đủ về mọi mặt lập pháp, hành pháp và tư pháptrong phạm vi lãnh thổ của quốc gia đó.',ans2:'B. Quốc gia thể hiện chủ quyền của mình trên mọi phương diện kinh tế, chính trị, quân sự, ngoại giao.',ans3:'C. Quyền làm chủ vận mệnh của mỗi dân tộc, quyền quyết định chế độ chính trị, con đường phát triển.',ans4:'D. Cả A và B.'};quest2[77]={key:'d',q:'Các quốc gia thể hiện chủ quyền trên những Phương diện nào:',ans1:'A. Kinh tế.',ans2:'B. Chính trị.',ans3:'C. Quân sự, ngoại giao.',ans4:'D. Tất cả lựa chọn trên.'};quest2[78]={key:'c',q:'Chủ quyền lãnh thổ quốc gia là:',ans1:'A. Một bộ phận của chủ quyền quốc gia, khẳng định quyền làm chủ vùng đất, biển, vùng trời của quốcgia.',ans2:'B. Quyền thiêng liêng bất khả xâm phạm, quyết định mọi vấn đề đối nội, đối ngoại của quốc gia.',ans3:'C. Một bộ phận của chủ quyền quốc gia, khẳng định quyền làm chủ của quốc gia đó trên vùng lãnhthổ của mình.',ans4:'D. Quyền thiêng liêng bất khả xâm phạm, quyết định mọi vấn đề chính trị, kinh tế, văn hóa, xã hội.'};quest2[79]={key:'c',q:'Nội dung xây dựng và bảo vệ chủ quyền lãnh thổ quốc gia:',ans1:'A. Đấu tranh làm thất bại các hoạt động phá hoại của các thế lực chống phá Việt Nam.',ans2:'B. Xây dựng và phát triển kinh tế, xã hội kết hợp với tăng cường củng cố quốc phòng an ninh.',ans3:'C. Xác lập và bảo vệ quyền lập pháp, hành pháp, tư pháp về mọi mặt trong phạm vi lãnh thổ.',ans4:'D. Đẩy mạnh công nghiệp hóa, hiện đại hóa đất nước; tích cực chủ động hội nhập quốc tế.'};quest2[80]={key:'a',q:'Nội dung xây dựng và bảo vệ chủ quyền lãnh thổ quốc gia:',ans1:'A. Bảo vệ sự toàn vẹn lãnh thổ của đất nước, bao gồm vùng đất, vùng trời, nội thị, lãnh hải và lãnhthổ quốc gia đặc biệt.',ans2:'B. Bảo vệ chủ quyền của quốc gia trên mọi phương diện kinh tế, chính trị, quân sự, ngoại giao.',ans3:'C. Bảo vệ đường lối đối nội và đối ngoại của Nhà nước ta không bị lệ thuộc vào bên ngoài.',ans4:'D. Tất cả lựa chọn trên.'};quest2[81]={key:'d',q:'Biên giới quốc gia Việt Nam bao gồm:',ans1:'A. Biên giới quốc gia trên đất liền, biển và trên không.',ans2:'B. Biên giới quốc gia trong lòng đất và trên biển.',ans3:'C. Biên giới quốc gia trên không, biển và trong lòng đất.',ans4:'D. Biên giới quốc gia trên đất liền, trên biển, trên không và trong lòng đất.'};quest2[82]={key:'d',q:'Biên giới quốc gia trên đất liền được xác định bằng:',ans1:'A. Các mốc quốc giới trên thực địa.',ans2:'B. Các tọa độ trên hải đồ.',ans3:'C. Các tọa độ trên bản đồ.',ans4:'D. Cả A và C.'};quest2[83]={key:'b',q:'Biên giới quốc gia trên biển được đánh dấu bằng:',ans1:'A. Các mốc quốc giới trên biển.',ans2:'B. Các tọa độ trên hải đồ.',ans3:'C. Các tọa độ trên bản đồ.',ans4:'D. Kinh độ, vĩ độ.'};quest2[84]={key:'c',q:'Nội dung xây dựng và bảo vệ biên giới quốc gia:',ans1:'A. Ưu tiên đầu tư xây dựng cơ sở hạ tầng, kinh tế khu vực biên giới.',ans2:'B. Ưu tiên đầu tư xây dựng cơ sở chính trị vững chắc ở khu vực biên giới.',ans3:'C. Ưu tiên đầu tư xây dựng khu vực biên giới vững mạnh toàn diện.',ans4:'D. Ưu tiên xây dựng lực lượng vũ trang địa phương ở khu vực biên giới.'};quest2[85]={key:'a',q:'Nội dung xây dựng và bảo vệ biên giới quốc gia:',ans1:'A. Tăng cường mở rộng quan hệ đối ngoại các cấp trên khu vực biên giới.',ans2:'B. Tăng cường, thực hiện tốt việc định canh, định cư cho nhân dân ở khu vực biên giới.',ans3:'C. Tập trung xây dựng các khu vực kinh tế quốc phòng dọc tuyến biên giới vững mạnh.',ans4:'D. Tập trung xây dựng các tổ chức chính trị vững chắc ở khu vực biên giới.'};quest2[86]={key:'b',q:'Nội dung xây dựng và bảo vệ biên giới quốc gia:',ans1:'A. Phối hợp với các nước láng giềng, ngăn chặn mọi âm mưu gây bạo loạn lật đổ của kẻ thù.',ans2:'B. Phối hợp với các nước, đấu tranh ngăn chặn mọi hành động phá hoại tình đoàn kết hữu nghịcác nước láng giềng.',ans3:'C. Kết hợp phát triển kinh tế với tăng cường an ninh để bảo vệ vững chắc tổ quốc.',ans4:'D. Kết hợp đấu tranh quân sự với đảm bảo an ninh chính trị, trật tự an toàn xã hội.'};quest2[87]={key:'b',q:'Xây dựng và bảo vệ chủ quyền lãnh thổ biên giới quốc gia được Đảng và Nhà nước ta xác định:',ans1:'A. Là một nội dung quan trọng của sự nghiệp đổi mới, công nghiệp hóa, hiện đại hóa đất nước.',ans2:'B. Là một nội dung quan trọng của sự nghiệp xây dựng và bảo vệ Tổ quốc Việt Nam XHCN.',ans3:'C. Là một nội dung quan trọng của chiến lược phát triển kinh tế trong thời kỳ mới.',ans4:'D. Là một nội dung quan trọng của chiến lược đối ngoại trong thời kỳ mới.'};quest2[88]={key:'c',q:'Quan điểm của Nhà nước ta về xây dựng bảo vệ chủ quyền lãnh thổ, biên giới quốc gia:',ans1:'A. Chủ quyền lãnh thổ, biên giới quốc gia là kết quả đấu tranh của hàng ngàn năm dựng nước.',ans2:'B. Chủ quyền lãnh thổ, biên giới quốc gia là yếu tố cơ bản nhất cho sự phát triển kinh tế xã hội.',ans3:'C. Chủ quyền lãnh thổ, biên giới quốc gia là thiêng liêng, bất khả xâm phạm của dân tộc Việt Nam.',ans4:'D. Chủ quyền lãnh thổ, biên giới quốc gia là tài sản vô cùng quý giá của dân tộc Việt Nam.'};quest2[89]={key:'c',q:'Quan điểm xây dựng biên giới hòa bình hữu nghị, ổn định của Đảng và Nhà nước ta thể hiện:',ans1:'A. Là vấn đề quan trọng cơ bản và lâu dài trong sự nghiệp cách mạng của Đảng ta.',ans2:'B. Là quan điểm nhất quán trong sự nghiệp công nghiệp hóa hiện đại hóa đất nước.',ans3:'C. Là quan điểm nhất quán, phù hợp với lợi ích, luật pháp của Việt Nam và công ước quốc tế.',ans4:'D. Là xây dựng biên giới hòa bình hữu nghị, ổn định phù hợp với đường lối đối ngoại của nước ta.'};quest2[90]={key:'b',q:'Quan điểm của Đảng, Nhà nước ta về việc giải quyết các vấn đề tranh chấp lãnh thổ, biên giới:',ans1:'A. Thông qua các cơ quan tài phán và công ước của Liên Hợp Quốc về lãnh thổ, biên giới.',ans2:'B. Thương lượng hòa bình, tôn trọng độc lập, chủ quyền toàn vẹn lãnh thổ và lợi ích chính đángcủa nhau.',ans3:'C. Bằng con đường ngoại giao trên tinh thần bình đẳng đôi bên cùng có lợi.',ans4:'D. Bằng nhiều biện pháp kể cả biện pháp sử dụng vũ lực để giải quyết tranh chấp.'};quest2[91]={key:'d',q:'Xây dựng và bảo vệ chủ quyền lãnh thổ, biên giới quốc gia:',ans1:'A. Là sự nghiệp của toàn dân.',ans2:'B. Dưới sự lãnh đạo của Đảng.',ans3:'C. Sự quản lý thống nhất của Nhà nước.',ans4:'D. Cả ba lựa chọn trên.'};quest2[92]={key:'a',q:'Trong xây dựng và bảo vệ chủ quyền lãnh thổ, biên giới quốc gia, lực lượng nào là nòng cốt:',ans1:'A. Quân đội nhân dân.',ans2:'B. Công an nhân dân.',ans3:'C. Bộ đội địa phương.',ans4:'D. Dân quân tự vệ.'};quest2[93]={key:'b',q:'Trong xây dựng và bảo vệ chủ quyền lãnh thổ, biên giới quốc gia, lực lượng nào là nòng cốt chuyên trách:',ans1:'A. Bộ đội Hải quân.',ans2:'B. Bộ đội biên phòng.',ans3:'C. Cảnh sát biển.',ans4:'D. Dân quân tự vệ.'};quest2[94]={key:'b',q:'Đảng ta nhận định: trên thế giới hòa bình, hợp tác và phát triển là:',ans1:'A. Xu thế chủ đạo trong quan hệ giữa các dân tộc.',ans2:'B. Xu thế lớn trong quan hệ giữa các dân tộc.',ans3:'C. Xu thế chủ yếu trong quan hệ giữa các dân tộc.',ans4:'D. Xu thế quyết định sự phát triển của thế giới.'};quest2[95]={key:'d',q:'Quan hệ dân tộc, sắc tộc hiện nay trên thế giới vẫn diễn ra rất phức tạp, nóng bỏng ở phạm vi:',ans1:'A. Châu Phi và châu Mỹ La tinh.',ans2:'B. Châu Á và châu Âu.',ans3:'C. Các nước Xã hội chủ nghĩa.',ans4:'D.Từng quốc gia, khu vực và quốc tế.'};quest2[96]={key:'c',q:'Theo quan điểm của Chủ nghĩa Mác – Lênin, giải quyết vấn đề dân tộc được xác định:',ans1:'A. Vừa là quan điểm, vừa là phương châm của nhà nước Vô sản.',ans2:'B. Vừa là nhiệm vụ, vừa là phương thức của nhà nước XHCN.',ans3:'C. Vừa là mục tiêu, vừa là động lực của Cách mạng XHCN.',ans4:'D. Vừa là quan điểm, vừa là mục tiêu của nhà nước XHCN.'};quest2[97]={key:'b',q:'Nội dung giải quyết các vấn đề dân tộc theo quan điểm của Lênin:',ans1:'A. Các vấn đề dân tộc phải phân chia đẳng cấp rõ ràng.',ans2:'B. Các dân tộc hoàn toàn bình đẳng.',ans3:'C. Các dân tộc phải tự trị ly khai.',ans4:'D. Các dân tộc phải có nền văn hóa chung.'};quest2[98]={key:'d',q:'tư tưởng Hồ Chí Minh về dân tộc và giải quyết vấn đề dân tộc:',ans1:'A. Quan tâm nâng cao đời sống văn hóa, tinh thần đồng bào dân tộc thiểu số.',ans2:'B. Xây dựng mối quan hệ hài hòa giữa các dân tộc trong đại gia đình các dân tộc Việt Nam.',ans3:'C. Xây dựng quan hệ tốt đẹp giữa các dân tộc Việt Nam với các quốc gia dân tộc trên thế giới.',ans4:'D. Các dân tộc bình đẳng, đoàn kết, tôn trọng và giúp đỡ nhau cùng phát triển.'};quest2[99]={key:'d',q:'Một trong các đặc trƣng của các dân tộc ở Việt Nam là:',ans1:'A. Các dân tộc thiểu số ở Việt Nam cư trú du canh, du cư.',ans2:'B. Các dân tộc thiểu số ở Việt Nam cư trú tập trung ở Miền Bắc.',ans3:'C. Các dân tộc thiểu số ở Việt Nam cư trú tập trung ở Tây Nguyên.',ans4:'D. Các dân tộc thiểu số ở Việt Nam cư trú phân tán và xen kẽ.'};quest2[100]={key:'c',q:'Một trong các đặc trƣng của các dân tộc ở Việt Nam là:',ans1:'A. Các dân tộc ở nước ta có quy mô dân số và trình độ phát triển trung bình.',ans2:'B. Các dân tộc ở nước ta có quy mô dân số và trình độ phát triển còn hạn chế.',ans3:'C. Các dân tộc ở nước ta có quy mô dân số và trình độ phát triển không đều.',ans4:'D. Các dân tộc ở nước ta có quy mô dân số và trình độ phát triển đồng đều.'};quest2[101]={key:'c',q:'Vấn đề dân tộc và đoàn kết dân tộc ở nước ta hiện nay được Đảng xác định:',ans1:'A. Là đặc biệt quan trọng của cách mạng nước ta.',ans2:'B. Là nguồn sức mạnh chủ yếu của đất nước ta.',ans3:'C. Có vị trí chiến lược lâu dài trong sự nghiệp cách mạng nước ta.',ans4:'D. Có ý nghĩa quyết định thắng lợi của sự nghiệp cách mạng nước ta.'};quest2[102]={key:'b',q:'Quan điểm chính sách dân tộc của Đảng, Nhà nước ta:',ans1:'A. Phát triển kinh tế, chăm lo đời sống vật chất, tinh thần của các dân tộc.',ans2:'B. Thực hiện bình đẳng, đoàn kết tôn trọng và giúp nhau cùng tiến bộ.',ans3:'C. Chống kỳ thị, hẹp hòi, chia rẽ dân tộc.',ans4:'D. Chống lợi dụng vấn đề dân tộc để gây mất ổn định đất nước.'};quest2[103]={key:'c',q:'Tôn giáo là một hình thái, ý thức xã hội, phản ánh hiện thực khách quan theo:',ans1:'A. Trào lưu của xã hội phù hợp với tư tưởng, tình cảm, niềm tin của con người.',ans2:'B. Quy luật phát triển của đời sống xã hội, được mọi người tin tưởng tham gia.',ans3:'C. Quan niệm hoang đường, ảo tưởng, phù hợp với tâm lý, hành vi của con người.',ans4:'D. Chuẩn mực đạo đức, truyền thống được con người tiếp nhận tin theo.'};quest2[104]={key:'c',q:'Tôn giáo có nguồn gốc từ các yếu tố:',ans1:'A. Kinh tế, xã hội, ý thức và hành vi.',ans2:'B. Chính trị, xã hôi, tinh thần và tâm lý.',ans3:'C. Kinh tế, xã hội, nhận thức và tâm lý.',ans4:'D. Chính trị, xã hội, kinh tế và tinh thần.'};quest2[105]={key:'a',q:'Tôn giáo có những tính chất:',ans1:'A. Tính lịch sử, tính quần chúng, tính chính trị.',ans2:'B. Tính lịch sử, tính quần chúng, tính khoa học.',ans3:'C. Tính lịch sử, tính nghệ thuật, tính chính trị.',ans4:'D. Tính kế thừa, tính quần chúng, tính chính trị.'};quest2[106]={key:'b',q:'Quan điểm của chủ nghĩa Mác – Lênin về giải quyết các vấn đề tôn giáo trong cách mạng XHCN:',ans1:'A. Quán triệt quan điểm lịch sử, toàn diện, cụ thể trong giải quyết vấn đề tôn giáo.',ans2:'B. Gắn liền với quá trình cải tạo xã hội cũ, xây dựng xã hội mới xã hội chủ nghĩa.',ans3:'C. Tôn trọng và bảo đảm quyền tự do tín ngưỡng của công dân.',ans4:'D. Kiên quyết bài trừ mê tín dị đoan và các hủ tục lạc hậu.'};quest2[107]={key:'d',q:'Quan điểm, chính sách tôn giáo của Đảng, Nhà nước ta hiện nay:',ans1:'A. Tôn giáo còn tồn tại lâu dài, là nhu cầu của một bộ phận nhân dân.',ans2:'B. Tôn giáo có những giá trị văn hóa, đạo đức tích cực, phù hợp với xã hội mới.',ans3:'C. Đồng bào tôn giáo là một bộ phận quan trọng của khối đại đoàn kết dân tộc.',ans4:'D. Tất cả đều đúng.'};quest2[108]={key:'c',q:'Nội dung cốt lõi của công tác tôn giáo ở Việt Nam là:',ans1:'A. Vận động quần chúng sống “kính chúa yêu nước”.',ans2:'B. Vận động quần chúng sống “phúc âm trong lòng dân tộc”.',ans3:'C. Vận động quần chúng sống “tốt đời, đẹp đạo”.',ans4:'D. Vận động quần chúng sống “từ bi, bác ái”.'};quest2[109]={key:'d',q:'Ở nước ta, công tác tôn giáo là trách nhiệm của:',ans1:'A. Toàn dân.',ans2:'B. Đảng và Nhà nước.',ans3:'C. Mặt trận Tổ quốc và các đoàn thể do Đảng lãnh đạo.',ans4:'D. Toàn bộ hệ thống chính trị do Đảng lãnh đạo.'};quest2[110]={key:'a',q:'Để vô hiệu hóa địch lợi dụng vấn đề dân tộc, tôn giáo ở Việt Nam của các thế lực thù địch, Phương pháp chung cơ bản nhất là:',ans1:'A. Tăng cường xây dựng củng cố đại đoàn kết toàn dân tộc.',ans2:'B. Tăng cường xây dựng củng cố các tổ chức chính trị vững mạnh.',ans3:'C. Thực hiện tốt chính sách xóa đói giảm nghèo.',ans4:'D. Thực hiện tốt chính sách đền ơn đáp nghĩa.'};quest2[111]={key:'b',q:'Giải pháp cơ bản đấu tranh phòng chống lợi dụng vấn đề dân tộc, tôn giáo là:',ans1:'A. Tạo mọi điều kiện cho đồng bào các dân tộc, các tôn giáo thực hiện tốt nghĩa vụ của mình đối vớiđất nước.',ans2:'B. Chăm lo nâng cao đời sống vật chất, tinh thần của đồng bào các dân tộc, các tôn giáo.',ans3:'C. Chú trọng công tác giáo dục thuyết phục đối với các già làng, trưởng bản, chức sắc tôn giáo.',ans4:'D. Kịp thời phát hiện, ngăn chặn đối phó có hiệu quả đối với những lực lượng phản động.'};quest2[112]={key:'d',q:'An ninh quốc gia là:',ans1:'A. Là sự bình yên của đất nước, cuộc sống ấm no hạnh phúc của nhân dân Việt Nam.',ans2:'B. Là sự ổn định, phát triển bền vững của chế độ XHCN và Nhà nước CHXHCN Việt Nam.',ans3:'C. Là sự bất khả xâm phạm độc lập chủ quyền, thống nhất và toàn vẹn lãnh thổ Tổ quốc.',ans4:'D. Cả B và C đúng.'};quest2[113]={key:'b',q:'Trong an ninh quốc gia, lĩnh vực nào là cốt lõi, xuyên suốt:',ans1:'A. An ninh kinh tế.',ans2:'B. An ninh chính trị.',ans3:'C. An ninh tư tưởng văn hóa.',ans4:'D. An ninh đối ngoại.'};quest2[114]={key:'d',q:'Bảo vệ an ninh quốc gia là:',ans1:'A. Phòng ngừa các hoạt động xâm hại an ninh quốc gia.',ans2:'B. Phát hiện các hoạt động xâm hại an ninh quốc gia.',ans3:'C. Đấu tranh ngăn chặn các hoạt động xâm hại an ninh quốc gia của các thế lực phản động.',ans4:'D. Phòng ngừa, phát hiện, ngăn chặn, đấu tranh làm thất bại các hoạt động xâm hại an ninh quốcgia.'};quest2[115]={key:'d',q:'Mục tiêu quan trọng về an ninh quốc gia:',ans1:'A. Lực lượng vũ trang nhân dân Việt Nam, các cơ quan Nhà nước và nhân dân.',ans2:'B. Những đối tượng, địa điểm, công trình, cơ sở về chính trị, an ninh, quốc phòng, kinh tế.',ans3:'C. Cơ sở KHKT, văn hóa, xã hội thuộc danh mục cần được bảo vệ theo quy định của pháp luật.',ans4:'D. Cả B và C đúng.'};quest2[116]={key:'a',q:'Nhiệm vụ bảo vệ an ninh quốc gia là:',ans1:'A. Bảo vệ chế độ chính trị và nhà nước CHXHCN Việt Nam, bảo vệ độc lập chủ quyền, thống nhấtvà toàn vẹn lãnh thổ của Tổ quốc.',ans2:'B. Bảo vệ chính quyền các cấp, sự nghiệp công nghiệp hóa, hiện đại hóa đất nước, sự nghiệp đổi mới.',ans3:'C. Bảo vệ bí mật của Nhà nước và các mục tiêu quan trọng về an ninh quốc gia.',ans4:'D. Bảo vệ an ninh trong lĩnh vực kinh tế, quốc phòng, đối ngoại và các lợi ích khác của quốc gia.'};quest2[117]={key:'b',q:'Nguyên tắc bảo vệ an ninh quốc gia là:',ans1:'A. Kết hợp bảo vệ an ninh chính trị với bảo vệ độc lập, chủ quyền thống nhất và toàn vẹn lãnh thổ Tổquốc.',ans2:'B. Tuân thủ hiến pháp, pháp luật, bảo đảm lợi ích của nhà nước, quyền và lợi ích hợp pháp của tổchức, cá nhân.',ans3:'C. Tuân thủ những quy định của luật quốc phòng, luật an ninh và những quy định của chính quyền.',ans4:'D. Kết hợp chặt chẽ phát triển kinh tế xã hội với bảo vệ các công trình quốc phòng an ninh.'};quest2[118]={key:'b',q:'Nguyên tắc bảo vệ an ninh quốc gia là:',ans1:'A. Kết hợp giữa bảo vệ an ninh tư tưởng với bảo vệ quyền lợi của nhân dân.',ans2:'B. Đặt dưới sự lãnh đạo của Đảng Cộng sản Việt Nam, sự quản lý thống nhất của Nhà nước.',ans3:'C. Kết hợp giữa phát triển với đấu tranh, làm thất bại các hoạt động xâm phạm an ninh quốc gia.',ans4:'D. Đặt dưới sự lãnh đạo, quản lý của lực lượng vũ trang nhân dân Việt Nam.'};quest2[119]={key:'c',q:'Cơ quan chuyên trách bảo vệ an ninh quốc gia là:',ans1:'A. Cơ quan chỉ đạo, chỉ huy và các đơn vị biên phòng, dân quân tự vệ, dự bị động viên.',ans2:'B. Cơ quan chỉ đạo tác chiến và các đơn vị an ninh quân đội, tình báo quân đội.',ans3:'C. Cơ quan chỉ đạo, chỉ huy và các đơn vị bảo vệ an ninh, tình báo, cảnh sát, cảnh vệ công an nhândân.',ans4:'D. Cơ quan chỉ đạo, chỉ huy và các đơn vị bộ đội hải quân, biên phòng, cảnh sát biển.'};quest2[120]={key:'a',q:'Các biện pháp bảo vệ an ninh quốc gia:',ans1:'A. Vận động quần chúng, pháp luật, ngoại giao, kinh tế, khoa học kỹ thuật, nghiệp vụ, vũ trang.',ans2:'B. Vận động quần chúng, lực lượng vũ trang, thực hiện chương trình quốc gia phòng chống tội phạm.',ans3:'C. Vận động quần chúng, đẩy mạnh xây dựng phong trào toàn dân bảo vệ an ninh tổ quốc ở cơ sở.',ans4:'D. Phát triển kinh tế, xây dựng nếp sống văn minh, giữ gìn an ninh, trật tự an toàn xã hội.'};quest2[121]={key:'d',q:'Nội dung bảo vệ an ninh tổ quốc gồm:',ans1:'A. Bảo vệ an ninh chính trị, an ninh quốc phòng, bảo vệ Đảng, Nhà nước, nhân dân.',ans2:'B. Bảo vệ an ninh đối ngoại, bảo vệ sự nghiệp công nghiệp hóa, hiện đại hóa.',ans3:'C. Bảo vệ các chính sách kinh tế xã hội, tôn giáo, dân tộc của Đảng và Nhà nước.',ans4:'D. Bảo vệ an ninh chính trị nội bộ, kinh tế, văn hóa, tư tưởng, dân tộc, tôn giáo, biên giới, thôngtin.'};quest2[122]={key:'a',q:'Bảo vệ an ninh chính trị nội bộ:',ans1:'A. Giữ gìn sự trong sạch, vững mạnh về mọi mặt của tổ chức Đảng, Nhà nước.',ans2:'B. Giữ gìn sự trong sạch, vững mạnh về mọi mặt của các tổ chức chính trị xã hội.',ans3:'C. Giữ gìn sự trong sạch, vững mạnh về mọi mặt của các tổ chức quần chúng.',ans4:'D. Giữ gìn sự trong sạch, vững mạnh về mọi mặt của lực lượng quân đội.'};quest2[123]={key:'c',q:'Trong bảo vệ an ninh quốc gia, nội dung nào là nhiệm vụ trọng yếu, hàng đầu, thường xuyên, cấp bách, toàn Đảng, toàn dân, của các ngành, các cấp:',ans1:'A. Bảo vệ an ninh biên giới.',ans2:'B. Bảo vệ an ninh kinh tế.',ans3:'C. Bảo vệ an ninh chính trị nội bộ.',ans4:'D. Bảo vệ an ninh dân tộc, tôn giáo.'};quest2[124]={key:'b',q:'Bảo vệ an ninh kinh tế là:',ans1:'A. Bảo vệ thành quả kinh tế, thành quả lao động sản xuất và đời sống của nhân dân.',ans2:'B. Bảo vệ sự ổn định, phát triển vững mạnh của nền kinh tế thị trường, định hướng XHCN.',ans3:'C. Bảo vệ sự ổn định, phát triển vững mạnh của nền kinh tế thị trường XHCN.',ans4:'D. Bảo vệ công cuộc đổi mới sự nghiệp công nghiệp hóa, hiện đại hóa đất nước.'};quest2[125]={key:'a',q:'Bảo vệ an ninh văn hóa tư tưởng là:',ans1:'A. Bảo vệ đúng đắn, vai trò chủ đạo của chủ nghĩa Mác – Lênin, tư tưởng Hồ Chí Minh.',ans2:'B. Bảo vệ các chủ trương, chính sách của Đảng, pháp luật của Nhà nước Việt Nam.',ans3:'C. Bảo vệ các phong tục tập quán, truyền thống, tín ngưỡng của các dân tộc Việt Nam.',ans4:'D. Bài trừ tư tưởng lạc hậu, văn hóa phẩm độc hại và các biểu hiện tiêu cực khác.'};quest2[126]={key:'d',q:'Nội dung giữ gìn trật tự an toàn xã hội:',ans1:'A. Đấu tranh phòng chống tội phạm, giữ gìn trật tự nơi công cộng, bảo đảm trật tự an toàn giao thông.',ans2:'B. Phòng ngừa tai nạn lao động, phòng chống thiên tai, bài trừ tệ nạn xã hội, bảo vệ môi trường.',ans3:'C. Phòng chống các phong tục cổ hủ, lạc hậu, thói hư, tật xấu.',ans4:'D. Cả A và B đúng.'};quest2[127]={key:'d',q:'Đảm bảo trật tự an toàn giao thông là nhiệm vụ của:',ans1:'A. Cảnh sát giao thông, thanh tra giao thông.',ans2:'B. Chính phủ và ủy ban nhân dân các cấp.',ans3:'C. Ủy ban an toàn giao thông quốc gia.',ans4:'D. Tất cả mọi người tham gia giao thông.'};quest2[128]={key:'c',q:'Hiện nay, Việt Nam khẳng định là đối tác của:',ans1:'A. Những quốc gia, vùng lãnh thổ, các tổ chức phi chính phủ giúp đỡ Việt Nam.',ans2:'B. Những nước XHCN và các nước đang phát triển đang giúp đỡ Việt Nam.',ans3:'C. Những ai chủ trương tôn trọng độc lập, chủ quyền và hợp tác bình đẳng cùng có lợi với ViệtNam.',ans4:'D. Những tổ chức, cá nhân tôn trọng, giúp đỡ tạo điều kiện để Việt Nam phát triển.'};quest2[129]={key:'b',q:'Đối tượng xâm phạm đến an ninh quốc gia:',ans1:'A. Bọn tội phạm hình sự, tội phạm kinh tế.',ans2:'B. Bọn gián điệp, bọn phản động.',ans3:'C. Các đối tượng xâm phạm trật tự an toàn xã hội.',ans4:'D. Các phần tử quá khích, gây rối.'};quest2[130]={key:'d',q:'Trong tình hình hiện nay, chúng ta cần tập trung đấu tranh chống lực lượng phản động:',ans1:'A. Bọn phản động lợi dụng tôn giáo dân tộc.',ans2:'B. Bọn phản động người Việt Nam ở nước ngoài.',ans3:'C. Bọn có tư tưởng quan điểm sai trái, thái hóa, biến chất.',ans4:'D. Tất cả đều đúng.'};quest2[131]={key:'d',q:'Đối tượng xâm phạm về trật tự an toàn xã hội cần tập trung đấu tranh:',ans1:'A. Bọn phản động trong và ngoài nước.',ans2:'B. Bọn tội phạm ma túy, tội phạm kinh tế.',ans3:'C. Bọn tội phạm hình sự.',ans4:'D. Cả B và C.'};quest2[132]={key:'c',q:'Quan điểm của Đảng, Nhà nước ta về bảo vệ an ninh quốc gia, trật tự an toàn xã hội:',ans1:'A. Đảng lãnh đạo trực tiếp, tuyệt đối về mọi mặt bảo vệ an ninh quốc gia.',ans2:'B. Kết hợp chặt chẽ giữa hoạt động của lực lượng công an với lực lượng quân đội.',ans3:'C. Kết hợp chặt chẽ nhiệm vụ xây dựng với nhiệm vụ bảo vệ Tổ quốc.',ans4:'D. Phát huy quyền làm chủ của nhân dân trên lĩnh vực bảo vệ an ninh quốc gia.'};quest2[133]={key:'a',q:'Quan điểm của Đảng, Nhà nước ta về bảo vệ an ninh quốc gia, trật tự an toàn xã hội:',ans1:'A. Bảo vệ an ninh quốc gia phải kết hợp chặt chẽ với giữ gìn trật tự an toàn xã hội.',ans2:'B. Bảo vệ an ninh quốc gia phải kết hợp chặt chẽ với phát triển kinh tế xã hội.',ans3:'C. Bảo vệ an ninh quốc gia là nhiệm vụ cấp bách hàng đầu.',ans4:'D. Tất cả đều đúng.'};quest2[134]={key:'b',q:'Trong bảo vệ an ninh quốc gia, trật tự an toàn xã hội, lực lượng nào là nòng cốt:',ans1:'A. Quân đội nhân dân.',ans2:'B. Công an nhân dân.',ans3:'C. Dân quân tự vệ.',ans4:'D. Quần chúng nhân dân.'};quest2[135]={key:'d',q:'Trách nhiệm của sinh viên trong công tác bảo vệ an ninh quốc gia, giữ gìn trật tự an toàn xã hội:',ans1:'A. Tích cực, tự giác tham gia các hoạt động cụ thể để bảo vệ an ninh quốc gia, giữ gìn trật tự an toànxã hội.',ans2:'B. Tích cực tham gia chiến dịch tình nguyện mùa hè do Đoàn thanh niên phát động.',ans3:'C. Tăng cường rèn luyện thể lực, học tập tốt môn học Giáo dục Quốc phòng – An ninh.',ans4:'D. Cả A và C.'};quest2[136]={key:'a',q:'Phòng ngừa tội phạm là:',ans1:'A. Phương hướng chính, là tư tưởng là cốt lõi trong công tác đấu tranh phòng chống tội phạm.',ans2:'B. Phương hướng chính, là tư tưởng chỉ đạo trong công tác đấu tranh phòng chống tội phạm.',ans3:'C. Nhiệm vụ chính, là tư tưởng xuyên suốt trong công tác đấu tranh phòng chống tội phạm.',ans4:'D. Nhiệm vụ trọng tâm, là nền tảng của công tác đấu tranh phòng chống tội phạm.'};quest2[137]={key:'b',q:'Phòng ngừa tội phạm mang ý nghĩa:',ans1:'A. Chính trị văn hóa sâu sắc.',ans2:'B. Chính trị xã hội sâu sắc.',ans3:'C. Chính trị giáo dục sâu sắc.',ans4:'D. Chính trị pháp luật sâu sắc.'};quest2[138]={key:'b',q:'Mục đích của phòng ngừa tội phạm là:',ans1:'A. Khắc phục, thủ tiêu các nguyên nhân, điều kiện của tình trạng phạm tội.',ans2:'B. Tìm ra các nguyên nhân, điều kiện của tình trạng phạm tội.',ans3:'C. Đề ra các biện pháp phù hợp để hạn chế tình trạng phạm tội.',ans4:'D. Xây dựng hoàn thiện hệ thống pháp luật và các văn bản dưới luật.'};quest2[139]={key:'a',q:'Một trong những nguyên nhân, điều kiện của tình trạng phạm tội:',ans1:'A. Hệ thống pháp luật hoàn thiện nhưng khung hình phạt các hành vi phạm tội còn nhẹ.',ans2:'B. Hệ thống pháp luật hoàn thiện nhưng hoạt động của các cơ quan tư pháp chưa tốt.',ans3:'C. Hệ thống pháp luật chưa hoàn thiện, việc thực thi pháp luật kém hiệu quả.',ans4:'D. Hệ thống pháp luật hoàn thiện nhưng việc thực thi pháp luật còn kém hiệu quả.'};quest2[140]={key:'c',q:'Chủ thể trong hoạt động phòng chống tội phạm gồm:',ans1:'A. Quốc hội và hội đồng nhân dân các cấp, chính phủ và ủy ban nhân dân các cấp.',ans2:'B. Các tổ chức xã hội, các tổ chức quần chúng tự quản, công dân.',ans3:'C. Các cơ quan bảo vệ pháp luật: công an, viện kiểm sát, tòa án.',ans4:'D. Tất cả đều đúng.'};quest2[141]={key:'d',q:'Chức năng của Quốc hội trong công tác đấu tranh phòng chống tội phạm là:',ans1:'A. Ban hành các văn bản pháp luật và giám sát việc tuân thủ pháp luật.',ans2:'B. Đề ra các chủ trương, biện pháp thích hợp đấu tranh phòng chống tội phạm.',ans3:'C. Chỉ đạo, điều hành các cơ quan hành pháp đấu tranh phòng chống tội phạm.',ans4:'D. Chỉ đạo các cơ quan hành pháp đấu tranh phòng chống tội phạm.'};quest2[142]={key:'a',q:'Chức năng của chính phủ và ủy ban nhân dân các cấp trong đấu tranh, phòng chống tội phạm là:',ans1:'A. Lãnh đạo, chỉ đạo công tác phòng chống tội phạm.',ans2:'B. Quản lý, điều hành, phối hợp, bảo đảm các điều kiện cần thiết.',ans3:'C. Ban hành các văn bản pháp luật và giám sát việc tuân thủ pháp luật.',ans4:'D. Truy tố, xét xử, cải tạo, tái hòa nhập cộng đồng đối tượng phạm tội.'};quest2[143]={key:'b',q:'Các cơ quan bảo vệ pháp luật gồm:',ans1:'A. Công an, quân đội, tòa án quân sự.',ans2:'B. Công an, bộ đội biên phòng, cảnh sát biển.',ans3:'C. Cảnh sát giao thông, kiểm soát quân sự.',ans4:'D. Công an, viện kiểm sát, tòa án.'};quest2[144]={key:'d',q:'Công dân với tƣ cách là chủ thể trong phòng chống tội phạm phải:',ans1:'A. Thực hiện tốt các quyền, nghĩa vụ của công dân được quy định trong hiến pháp.',ans2:'B. Quản lý, giáo dục các thành viên trong gia đình tôn trọng pháp luật.',ans3:'C. Vận động nhân dân đoàn kết cùng nhau phòng chống tội phạm.',ans4:'D. Tích cực tham gia các tổ chức quần chúng tự quản ở địa phương.'};quest2[145]={key:'a',q:'Nguyên tắc tổ chức hoạt động phòng chống tội phạm là:',ans1:'A. Nhà nước quản lý, kết hợp chủ động phòng ngừa với chủ động liên tục tấn công.',ans2:'B. Tuân thủ pháp luật, phối hợp và cụ thể, dân chủ, nhân đạo, khoa học và tiến bộ.',ans3:'C. Đảng lãnh đạo, nhà nước quản lý, phát huy quyền làm chủ của nhân dân.',ans4:'D. Cả A và B.'};quest2[146]={key:'d',q:'Phòng ngừa chung trong phòng chống tội phạm là:',ans1:'A. Tổng hợp tất cả các biện pháp về chính trị, kinh tế, văn hóa, pháp luật và giáo dục.',ans2:'B. Tổng hợp tất cả các biện pháp về chính trị, kinh tế, xã hội.',ans3:'C. Tổng hợp tất cả các biện pháp về tuyên truyền, giáo dục đạo đức, lối sống.',ans4:'D. Tổng hợp tất cả các biện pháp về tuyên truyền, giáo dục pháp luật.'};quest2[147]={key:'a',q:'Mục đích trong công tác phòng chống tệ nạn xã hội:',ans1:'A. Ngăn ngừa, xóa bỏ những nguyên nhân dẫn đến tệ nạn xã hội.',ans2:'B. Ngăn ngừa, chặn đứng những hậu quả xấu tác động đến bản sắc văn hóa dân tộc.',ans3:'C. Góp phần giữ vững ổn định chính trị, xã hội để kinh tế ngày càng phát triển.',ans4:'D. Phát hiện, đấu tranh, xử lý nghiêm minh những hành vi, hoạt động tệ nạn xã hội.'};quest2[148]={key:'d',q:'Mục đích trong công tác phòng chống tệ nạn xã hội là:',ans1:'A. Ngăn ngừa, chặn đứng, không để cho tệ nạn xã hội phát sinh, phát triển lan rộng đến địa bàn.',ans2:'B. Ngăn ngừa, từng bước xóa bỏ những nguyên nhân gây tệ nạn xã hội.',ans3:'C. Ngăn ngừa, chặn đứng những hậu quả xấu tác động đến bản sắc văn hóa dân tộc.',ans4:'D. Góp phần giữ vững ổn định chính trị, xã hội để kinh tế ngày càng phát triển.'};quest2[149]={key:'a',q:'Đặc điểm của tệ nạn xã hội:',ans1:'A. Để lại hậu quả nghiêm trọng cho xã hội.',ans2:'B. Để lại hậu quả lâu dài cho xã hội.',ans3:'C. Có tính gây lan nhanh trong xã hội.',ans4:'D. Hoạt động có tổ chức.'};quest2[150]={key:'c',q:'Đặc điểm của tệ nạn xã hội:',ans1:'A. Không có quan hệ với tội phạm hình sự, các hiện tượng tiêu cực xã hội khác.',ans2:'B. Tệ nạn xã hội là nguyên nhân chủ yếu dẫn đến phạm tội của các đối tượng trong xã hội.',ans3:'C. Có quan hệ chặt chẽ với tội phạm hình sự, các hiện tượng tiêu cực xã hội khác và có chuyển hóa lẫn nhau.',ans4:'D. Tệ nạn xã hội là nguyên nhân dẫn đến xóa mòn các giá trị đạo đức của dân tộc.'};quest2[151]={key:'c',q:'Thái độ của sinh viên đối với ngƣời mắc phải các tệ nạn xã hội:',ans1:'A. Xa lánh, coi thường, không cần quan tâm.',ans2:'B. Giúp đỡ họ về tinh thần, hỗ trợ về vật chất.',ans3:'C. Cảm thông, chia sẻ, động viên an ủi, giúp đỡ họ.',ans4:'D. Cảm hóa, giáo dục, để họ trở thành công dân có ích cho gia đình và xã hội.'};quest2[152]={key:'d',q:'Trong các tệ nạn xã hội, tệ nạn dẫn đến tội phạm nghiêm trọng:',ans1:'A. Tệ nạn mê tín dị đoan.',ans2:'B. Tệ nạn mại dâm.',ans3:'C. Tệ nạn ma túy.',ans4:'D. Tệ nạn cờ bạc.'};quest2[153]={key:'c',q:'Hình thức sử dụng ma túy phổ biến trong một bộ phận giới trẻ hiện nay:',ans1:'A. Chủ yếu là hút, hít ma túy.',ans2:'B. Chủ yếu là tiêm, chích thuốc phiện, heroin.',ans3:'C. Chủ yếu là sử dụng ma túy tổng hợp (thuốc lắc).',ans4:'D. Chủ yếu là tiêm chích ma túy.'};quest2[154]={key:'c',q:'Hậu quả và tác hại của tệ nạn mại dâm là:',ans1:'A. Trái với chuẩn mực đạo đức xã hội, thuần phong mỹ tục của dân tộc ta.',ans2:'B. Phá vỡ hạnh phúc gia đình, tác hại đến nòi giống hôm nay và mai sau.',ans3:'C. Làm ảnh hưởng đến đạo đức, nhân phẩm, giá trị con người và hạnh phúc gia đình.',ans4:'D. Làm xói mòn đạo đức dân tộc, là một trong những nguyên nhân dẫn đến căn bệnh HIV/AIDS.'};quest2[155]={key:'d',q:'Tệ nạn cờ bạc có mối quan hệ chặt chẽ với:',ans1:'A. Các hành vi và hiện tượng trong xã hội.',ans2:'B. Nhóm tệ nạn xã hội như mê tín dị đoan, ma túy.',ans3:'C. Tội phạm hình sự và các hiện tượng tiêu cực khác.',ans4:'D. Có tính độc lập, ít liên quan tới các tệ nạn xã hội khác.'};quest2[156]={key:'c',q:'Tác hại của tệ nạn cờ bạc đối với xã hội và cộng đồng:',ans1:'A. Gây hậu quả lớn về kinh tế văn hóa, xã hội và môi trường.',ans2:'B. Gây mất an ninh chính trị, trật tự an toàn xã hội ở cộng đồng dân cư.',ans3:'C. Gây tác hại lớn cho đời sống xã hội và khó khăn cho công tác giữ gìn trật tự xã hội.',ans4:'D. Gây tổn thất về kinh tế, hạnh phúc gia đình và lây lan trong xã hội.'};quest2[157]={key:'c',q:'Trách nhiệm của sinh viên đối với các hành vi tệ nạn xã hội:',ans1:'A. Tích cực tham gia các phong trào của nhà trường.',ans2:'B. Tích cực học tập và nâng cao tri thức, hiểu biết pháp luật, không tham gia vào các tệ nạn xã hội.',ans3:'C. Phát hiện các hành vi hoạt động tệ nạn xã hội báo cáo kịp thời cho nhà trường hoặc lực lượng công an cơ sở.',ans4:'D. Tích cực tham gia cùng các lực lượng bảo vệ trật tự trị an trường lớp.'};/* harmony default export */ __webpack_exports__["a"] = (quest2);

